import React, { useContext, useState } from "react";
import ProjectContext from "../../context/ProjectContext";
import { ChooseClipartModal } from "./ModalChild/ChooseClipartModal";
import downarrow from "../../assets/images/downarrow.svg";
import { Modal } from "react-bootstrap";
import { SVGDownArrow, SVGUpArrow } from "../../components/SVGs/SVGs";

export const UploadClipArts = ({ showClipartModal }) => {
  const { allClipArts, handleUploadClipArts, handleAllCliparts } =
    useContext(ProjectContext);
  const [chooseClipartModal, setChooseClipartModal] = useState(false);
  const [checkedClipArt, setCheckedClipArt] = useState();
  const [countSelectd, setCountSelectd] = useState(0);
  const [clipArtExpand, setClipArtExpand] = useState(false);
  const [isAllSelected, setIsAllSelected] = useState(false);
  const [selectedTag, setSelectedTag] = useState("All");
  const [searchInput, setSearchInput] = useState("");
  const [clipArts, setClipArts] = useState([...allClipArts]);
  const [clipartCat, setClipartCat] = useState([]);

  const resetCategory = () => {
    let temp = [...clipArts];
    temp.forEach((c) => {
      c.isSelected = false;
    });
    setClipArts(temp);
  };

  // select category handle
  const onSelectCategoryClick = (category) => {
    console.log(category);
    // resetCategory();
    if (category == "All") {
      setSelectedTag("All");
      setClipartCat([...allClipArts]);
      // handleAllCliparts([...allClipArts]);
    } else {
      setSelectedTag(category);
      let matched = allClipArts.filter((clipArt) => {
        return category === clipArt.type;
      });
      // console.log(matched);
      setClipartCat(matched);
      // handleAllCliparts(matched);
    }
  };

  // single selection clipart handle
  const handleSelected = (isCheck, id) => {
    let totalSelected = [];
    for (let i = 0; i < allClipArts.length; i++) {
      if (allClipArts[i].id == id) {
        allClipArts[i] = { ...allClipArts[i], isChecked: isCheck };
      }
    }
    totalSelected = allClipArts.filter((check) => {
      if (check.isChecked) {
        return true;
      }
    });
    if (allClipArts.length == totalSelected.length) {
      setIsAllSelected(true);
    } else {
      setIsAllSelected(false);
    }
    // console.log(totalSelected.length);
    setCountSelectd(totalSelected.length);
    handleUploadClipArts(allClipArts);
  };

  // all selection clipart handle
  const handleSelectAll = (isCheck) => {
    let totalSelected = [];
    for (let i = 0; i < allClipArts.length; i++) {
      allClipArts[i] = { ...allClipArts[i], isChecked: isCheck };
    }
    totalSelected = allClipArts.filter((check) => {
      if (check.isChecked) {
        return true;
      }
    });
    if (allClipArts.length == totalSelected.length) {
      setIsAllSelected(true);
    } else {
      setIsAllSelected(false);
    }
    setCountSelectd(totalSelected.length);
    handleUploadClipArts(allClipArts);
  };

  const setIsSelected = (temp, boolean) => {
    temp.forEach((c) => {
      c.isSelected = boolean;
    });
  };

  // input search handle
  const onSearchChange = (e) => {
    setSearchInput(e.target.value);
  };

  return (
    <>
      <div className="buttonSection border-0 pb-0">
        <div className="buttonBar d-flex align-items-start justify-content-between">
          <div>
            <p className="buttonTitle" onClick={() => setClipArtExpand(!clipArtExpand)}>Clipart</p>
            <p className="buttonSubTitle">
              Select the clipart you want to be available on your store
            </p>
          </div>
          <span
            className="buttonArrow"
            onClick={() => setClipArtExpand(!clipArtExpand)}
          >
            {clipArtExpand ? <SVGUpArrow /> : <SVGDownArrow />}
          </span>
        </div>
        <div className={clipArtExpand ? "collapse show" : "collapse "} id="uploadClipart">
          <div className="collapseContent">
            <div className="d-flex align-items-center uploadBtn">
              <button
                className="btn btn-outline-dark"
                data-toggle="modal"
                data-target="#chooseClipart"
                onClick={() => setChooseClipartModal(true)}
              >
                <b>Select clipart</b>
              </button>
            </div>
            {countSelectd > 0 && (
              <div className="card card-body  bg-light">
                <span className="uploadStatus darkStatus">
                  <strong>{countSelectd}</strong> Cliparts selected
                </span>
              </div>
            )}
            {/* <p className="paragraphSubTitle">
              Select the clipart you want to be available on your store
            </p> */}
          </div>
        </div>
      </div>

      {/* select clipArt modal */}
      <Modal
        show={chooseClipartModal}
        className="fade chooseClipartPreview"
        onHide={() => setChooseClipartModal(false)}
      >
        <ChooseClipartModal
          hide={() => setChooseClipartModal(false)}
          countSelectd={countSelectd}
          setCountSelectd={setCountSelectd}
          setIsAllSelected={setIsAllSelected}
          isAllSelected={isAllSelected}
          onSelectCategoryClick={onSelectCategoryClick}
          searchInput={searchInput}
          setSearchInput={setSearchInput}
          selectedTag={selectedTag}
          setSelectedTag={setSelectedTag}
          // handleSelected={handleSelected}
          // handleSelectAll={handleSelectAll}
          allClipArts={allClipArts}
          searchInput={searchInput}
          onSearchChange={onSearchChange}
          clipartCat={clipartCat.length == 1 ? clipartCat : allClipArts}
        />
      </Modal>
    </>
  );
};
