import React, { useContext } from "react";
import ProjectContext from "../../../../../context/ProjectContext";
import { Items } from "./Items";

export const MainSection = ({
  activeTab,
  handleSelectItem,
  activeItem,
  clipArtsData,
  handleSelectedArtWorks,
}) => {
  const { uploadsDesign, uploadsLogo } = useContext(ProjectContext);

  return (
    <>
      {(() => {
        switch (activeTab) {
          case "design":
            return (
              <Items
                itemsArray={uploadsDesign}
                handleSelectItem={handleSelectItem}
                activeItem={activeItem}
                activeTab={activeTab}
                handleSelectedArtWorks={handleSelectedArtWorks}
              />
            );
          case "logo":
            return (
              <Items
                itemsArray={uploadsLogo}
                handleSelectItem={handleSelectItem}
                activeItem={activeItem}
                activeTab={activeTab}
                handleSelectedArtWorks={handleSelectedArtWorks}
              />
            );
          case "clipart":
            return (
              <Items
                itemsArray={clipArtsData}
                handleSelectItem={handleSelectItem}
                activeItem={activeItem}
                activeTab={activeTab}
                handleSelectedArtWorks={handleSelectedArtWorks}
              />
            );
          default: {
            return;
          }
        }
      })()}
    </>
  );
};
