import React from "react";
import { Button, Modal } from "react-bootstrap";

const ConfirmModal = ({
  title,
  description,
  handleConfirm,
  handleClose,
  takeScreenshot,
  disabled,
}) => {
  return (
    <>
      <Modal.Header>
        <div>
          <h4>{title}</h4>
        </div>
      </Modal.Header>
      <Modal.Body>
        <div>{description}</div>
      </Modal.Body>
      <Modal.Footer>
        <div>
          <button
            onClick={handleClose}
            className="btn btn-secondary btn-link d-none d-md-inline pr-3"
            disabled={disabled}
          >
            <b>No</b>
          </button>
          <button
            onClick={handleConfirm}
            disabled={disabled}
            disabled={takeScreenshot}
            className="btn btn-secondary"
          >
            {disabled ? <b>Saving...</b> : <b>Yes</b>}
          </button>
        </div>
      </Modal.Footer>
    </>
  );
};

export default ConfirmModal;
