import React, { useContext, useState, useEffect } from "react";
import { SearchBox } from "../../ModalChild/SearchBox";
import _ from "lodash";
import {
  SVGGridView,
  SVGListView,
  SVGMinus,
  SVGPlus,
} from "../../../../components/SVGs/SVGs";
import { ButtonGroup, Button, Collapse } from "react-bootstrap";
import ProjectContext from "../../../../context/ProjectContext";
import { CollapsCat } from "../../../../components/DropDownModal/CollapsCat";
import axios from "axios";
import { CollapsMultiSelect } from "../../../../components/DropDownModal/CollapsMultiSelect";

export const Name = ({ 
  setDataArray, dataArray 
}) => {
  const {
    productVarient,
    productNaming,
    handleProductNaming,
    multiSelectedProduct,
    allCategory,
    handleMultiProduct,
    checkedFinalProduct,
    handleCheckedFinalProduct,
    handleCustomizedProduct,
    customizedProduct,
    selectedProductCategories,
    handleCustomProducts,
    customProducts,
    handleProductVarient,
    updateProductVariant,
    handleUpdateProductVariant
  } = useContext(ProjectContext);

  const [viewStyle, setViewStyle] = useState("grid");
  const [productName, setProductName] = useState("");
  const [selectedProducts, setSelectedProducts] = useState(0);
  const [listItemsArray, setListItemsArray] = useState([]);
  const [catAllPro, setCatAllPro] = useState([]);
  const [allProduct, setAllProduct] = useState([...productVarient]);
  const [nameMapedArray, setNameMapedArray] = useState([...productVarient]);
  const [finalProduct, setFinalProduct] = useState();
  const [countProduct, setCountProduct] = useState(0);
  // const [dataArray, setDataArray] = useState([...productVarient]);

  const [searchInput, setSearchInput] = useState("");


  useEffect(()=>{
    handleUpdateProductVariant(productVarient)
  },[productVarient])
  useEffect(()=>{
    if(productVarient.length > 0){
      setDataArray([...productVarient]);
    }
  },[productVarient])
 
  const onProductNameChange = (e, item) => {
    let inputVal = e.target.value;

    for (let i = 0; i < updateProductVariant.length; i++) {
      if (updateProductVariant[i].productVarientId == item.productVarientId) {
        updateProductVariant[i] = {
          ...updateProductVariant[i],
          name: inputVal,
        };
      }
    }
    handleUpdateProductVariant(updateProductVariant);
    handleCustomizedProduct([...updateProductVariant]);
  };

  const handleSelectedProducts = (status, item) => {
    let tempProduct = [];
    for (let i = 0; i < updateProductVariant.length; i++) {
      if (updateProductVariant[i].productVarientId == item.productVarientId) {
        updateProductVariant[i] = {
          ...updateProductVariant[i],
          isFinalProductSelected: status,
        };
      }
    }
    tempProduct = updateProductVariant.filter(
      (i) => i.isFinalProductSelected && i
    );
    setCountProduct(tempProduct.length);
    handleCustomizedProduct([...tempProduct]);
  };

  const checkProductItem = (id) => {
    let filterArray = customizedProduct.filter((item) => {
      if (item.productVarientId == id) {
        return item;
      } else {
        return false;
      }
    });
    if (filterArray.length > 0) {
      return true;
    } else {
      return false;
    }
  };

  useEffect(() => {
    handleCustomizedProduct([...productVarient]);
  }, [productVarient]);

  
  const checkFinalProduct = (id) => {
    let checkedProduct = checkedFinalProduct.filter(function (i) {
      if (i.productVarientId === id) {
        return i;
      } else {
        return false;
      }
    });
    if (checkedProduct.length === 1) {
      return true;
    } else {
      return false;
    }
  };
  // const handleClick = (id) => {
  //   // let tempsArray = [...dataArray];

  //   tempsArray.forEach((element) => {
  //     if (element.category_id === id) {
  //       console.log(element.show);
  //       element.show = !element.show;
  //     }
  //   });
  //   // setDataArray(tempsArray);
  // };
 
  const onSearchChange = (e) => {
    setSearchInput(e.target.value);
  };

  const allCheckedCategory = (status) => {
    if (status) {
      // setUpdateProductVariant([]);
      handleUpdateProductVariant([...productVarient]);
      setCountProduct(productVarient.length);
    } else {
      setCountProduct(0);
      handleUpdateProductVariant([]);
    }
  };

  const checkerAllSelect = () => {
    if (updateProductVariant.length === productVarient.length) {
      return true;
    } else {
      return false;
    }
  };

  

  const catCheckStatus = (id) => {
    let fillterItem = productVarient.filter(function (item) {
      if (item.category_id === id) {
        return item;
      } else {
        return false;
      }
    });
    if (fillterItem.length > 0) {
      return true;
    } else {
      return false;
    }
  };
 

  const handleCheckCategory = (item, status) => {
    let selectArray = [...productVarient];
    if (status) {
      let filterArray = selectArray.filter((elm) => {
        if (item.categoryName == elm.categoryName) {
          return elm;
        } else {
          return false;
        }
      });
      handleUpdateProductVariant(updateProductVariant.concat(filterArray));
    } else {
      let filterArray = updateProductVariant.filter((elm) => {
        if (item.categoryName !== elm.categoryName) {
          return elm;
        } else {
          return false;
        }
      });
      handleUpdateProductVariant(filterArray);
    }
  };

  const checkCategorystatus = (name) => {
    let fillterItem = updateProductVariant.filter(function (item) {
      if (item.categoryName === name) {
        return item;
      } else {
        return false;
      }
    });
    if (fillterItem.length > 0) {
      return true;
    } else {
      return false;
    }
  };

  useEffect(() => {
    setCountProduct(updateProductVariant.length);
  }, [updateProductVariant]);
  
  useEffect(()=>{
    let filterData=_.chain(productVarient)
    .groupBy("categoryName")
    .map((value,key)=>({categoryName:key,products:value}))
    .value();
    setDataArray(filterData);
  },[productVarient,updateProductVariant])

  return (
    <>
      <div className="p-4">
        <SearchBox
          placeholder="Search categories, product names, product id"
          value={searchInput}
          onChange={(e) => onSearchChange(e)}
        />
      </div>

      <div
        className="d-lg-flex flex-grow-1 pt-4 nameTabRow"
        style={{ justifyContent: "space-around", alignItems: "start" }}
      >
        <div className="left-col pt-0">
          <div>
            <h4>Select category</h4>
            <ul className="nameTabCatList">
              <li className="nameTabCatListSelectAll">
                <label class="checkInput">
                  <input
                    type="checkbox"
                    onChange={(e) => allCheckedCategory(e.target.checked)}
                    checked={checkerAllSelect()}
                  />
                  <span className="checkInputCheck "></span>
                  <span class="checkInputText">Select All</span>
                </label>
              </li>
              {dataArray &&
                dataArray.map((item, index) => {
                  return (
                    <>
                      <li
                      // onClick={() => handleClick(item.category_id)}
                      >
                        <label class="checkInput">
                          <input
                            type="checkbox"
                            name={item.name}
                            // value={item}
                            onChange={(e) =>
                              handleCheckCategory(item, e.target.checked)
                            }
                            checked={checkCategorystatus(item.categoryName)}
                            // checked={item.isFinalCategorySelected}
                          />
                          <span className="checkInputCheck"></span>
                          <span class="checkInputText">
                            {item.categoryName}
                          </span>
                        </label>
                      </li>
                    </>
                  );
                })}
            </ul>
          </div>
        </div>
        <div className="right-col bgWhite border-left pt-1 pl-lg-5">
          {/* mt-4 */}
          <div className="d-flex justify-content-between">
            <h4>
              Select products{" "}
              <span className="text-muted font-weight-light f15 pl-2">
                {countProduct}/{productVarient.length}
              </span>
            </h4>
            <ButtonGroup className="listGridBtnGroup">
              <Button
                variant="link"
                onClick={() => setViewStyle("list")}
                className={viewStyle === "list" && "active"}
              >
                <SVGListView />
              </Button>
              <Button
                variant="link"
                onClick={() => setViewStyle("grid")}
                className={viewStyle === "grid" && "active"}
              >
                <SVGGridView />
              </Button>
            </ButtonGroup>
          </div>
          {/* <span>Total selected</span> */}
          <div className="row chooseProductArtPreview">
            <div className="col-md-12 ">
              <div
                className={
                  viewStyle === "list"
                    ? "info-block block-info clearfix filterItemsGrid chooseProductName filterItemsList"
                    : "info-block block-info clearfix filterItemsGrid chooseProductName"
                }
              >
                {updateProductVariant.length > 0 &&
                  updateProductVariant
                    .filter((item) => {
                      if (searchInput == "") {
                        return item;
                      } else if (
                        item.name
                          .toLowerCase()
                          .includes(searchInput.toLowerCase())
                      ) {
                        return item;
                      } else if (
                        item.productVarientId.toString().includes(searchInput)
                      ) {
                        return item;
                      }
                    })
                    .map((item) => (
                      <div
                        // data-toggle="buttons"
                        // className="btn-group"
                        // key={item.id}
                      >
                        <label className="btn ">
                          <div className="items">
                            <div className="isSelected ">
                              <label className="checkInput sm">
                                <input
                                  type="checkbox"
                                  // checked={checkFinalProduct(
                                  //   item.productVarientId
                                  // )}
                                  checked={checkProductItem(item.productVarientId)}
                                  onChange={(e) =>
                                    handleSelectedProducts(
                                      e.target.checked,
                                      item
                                    )
                                  }
                                />
                                <span className="checkInputCheck"></span>
                              </label>
                            </div>

                            <img
                              src={item.finalProductImageUrl}
                              alt="sampleImage"
                              className="img-fluid"
                            />
                            <textarea
                              type="text"
                              rows="2"
                              className="form-control"
                              placeholder="Add name here"
                              value={productName ? productName : item.name}
                              onChange={(e) => onProductNameChange(e, item)}
                            ></textarea>
                          </div>
                        </label>
                      </div>
                    ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};




