import React, { useState, useContext, useEffect, useRef } from "react";
import { Modal, Collapse } from "react-bootstrap";
import { toPng } from "html-to-image";
import { SVGCustomizer } from "../../components/SVGs/SVGs";
import sampleImg from "../../assets/images/selectProductsTShirt.png";
import { SwitchClipArtButton } from "../../components/SwitchClipArtButton";
import { SelectProductPreviewModal } from "../../views/Customizer/Modal/SelectProductPreviewModal";
import ProjectContext from "../../context/ProjectContext";
import { SingleSelectionModal } from "../DropDownModal/SingleSelectionModal";
import ImageCustomizer from "../../views/Customizer/ImageCustomizer/index";

export const ProductCustomizeBox = ({
  selectedArts,
  finalArray = [],
  setFinalArray,
  handleShuffle,
  shuffleUrl,
  selectedProducts = [],
  onProductSelection,
  customizeProductModal
}) => {
  const {
    selectedProduct,
    clipArts,
    uploadsLogo,
    uploadsDesign,
    customerSingleSelectedProduct,
  } = useContext(ProjectContext);
  const [selectproductPreviwModal, setSelectproductPreviwModal] =
    useState(false);
  const [open, setOpen] = useState(false);
  const [customerSelectedProduct, setCustomerSelectedProduct] =
    useState(sampleImg);

  const [demoImg, setDemoImg] = useState("");
  const [refreshCanvas, setRefreshCanvas] = useState(false);
  const [timer, setTimer] = useState(null);
  const selectedProductImageRef = useRef();
  const [updateTime, setUpdateTime] = useState();
  const [selectedProd, setSelectedProd] = useState({
    width: "",
    height: ""
  });

  const getUrl = (temp) => {
    let random = Math.floor(Math.random() * temp.length);
    let url = "";
    temp.forEach((u, index) => {
      if (index === random) {
        url = u.url;
      }
    });
    return url;
  };

  const ifChecked = (aRRay) => {
    let temp = [];
    aRRay.forEach((clipart) => {
      if (clipart.isChecked) {
        temp.push({ url: clipart.url });
      }
    });
    return temp;
  };

  useEffect(() => {
    setRefreshCanvas(false);
    setTimeout(() => {
      setTimer(null);
      setRefreshCanvas(true);
      setUpdateTime("updated");
    }, 500);
  }, [selectedProducts, customizeProductModal, shuffleUrl, selectedArts, uploadsLogo, uploadsDesign]);

  useEffect(() => {
    let obj = {
      width: Math.round((selectedProduct.side_images_info[1].drawing_area_width * selectedProductImageRef.current.width) / 100),
      height: Math.round((selectedProduct.side_images_info[1].drawing_area_height * selectedProductImageRef.current.height) / 100)
    };
    setSelectedProd(obj);
    setUpdateTime("");
  }, [selectedProducts, updateTime]);

  return (
    <>
      <div className="productSideLeft">
        <div className="imageViewer">
          <button
            className="btn btn-clear selectorBtn"
            type="button"
            onClick={() => setOpen(!open)}
            aria-controls="example-collapse-text"
            aria-expanded={open}
          >
            <SVGCustomizer />
          </button>
          <SingleSelectionModal
            openModal={open}
            setOpen={setOpen}
            onProductSelection={onProductSelection}
          />
          <div
            className="imageContainer w-100"
            id="imageContainer"
            style={{ position: "relative" }}
          >
            {selectedProducts.length > 0 ? (
              <>
                {selectedProduct.image ? (
                  <img
                    ref={selectedProductImageRef}
                    className="img-fluid w-100 selectedProductImage"
                    src={selectedProduct.side_images_info[1].image}
                    alt={selectedProduct.name}
                  />
                ) : (
                  <img
                    className="img-fluid w-100 selectedProductImage"
                    src={selectedProduct.url}
                    alt={selectedProduct.name}
                  />
                )}
                {refreshCanvas && (
                  <ImageCustomizer
                    top={
                      selectedProduct.side_images_info
                        ? selectedProduct.side_images_info[1].drawing_area_top
                        : 0
                    }
                    left={
                      selectedProduct.side_images_info
                        ? selectedProduct.side_images_info[1].drawing_area_left
                        : 0
                    }
                    width={
                      selectedProduct.side_images_info
                        ? selectedProduct.side_images_info[1].drawing_area_width
                        : 0
                    }
                    height={
                      selectedProduct.side_images_info
                        ? selectedProduct.side_images_info[1]
                          .drawing_area_height
                        : 0
                    }
                    clipartUrl={finalArray[shuffleUrl]?.url}
                    id="hompageCanvas"
                    dimensionsCanvas={selectedProd}
                  />
                )}
              </>
            ) : (
              "Customization  area is not defined"
            )}
          </div>
          {finalArray.length > 0 && (
            <SwitchClipArtButton handleShuffle={handleShuffle} />
          )}
        </div>
        <p className="messageText">
          *This is a default selection of design for reference only
        </p>
      </div>
      <Modal
        show={selectproductPreviwModal}
        onHide={() => setSelectproductPreviwModal(false)}
        size="lg"
        dialogClassName=""
        className="selectProductPreviwModal customize"
      >
        <SelectProductPreviewModal handleHide={setSelectproductPreviwModal} />
      </Modal>
    </>
  );
};