import React, { useContext, useEffect, useState } from "react";
import { Row, Col } from "react-bootstrap";
import Artwork1 from "../../../../../assets/images/artwork1.png";
import { HeaderBarCustomButtons } from "./HeaderBarCustomButtons";
import { SVGSearch } from "../../../../../components/SVGs/SVGs";
import ProjectContext from "../../../../../context/ProjectContext";

export const HeaderBar = ({
  handaleTab,
  activeClass,
  selectedItem,
  activeTab,
  setSelectedItem,
  setActiveItem,
}) => {
  const { uploadsDesign, allClipArts, uploadsLogo } =
    useContext(ProjectContext);
  const [defoultItem, setDefoultItem] = useState(
    uploadsDesign.length > 0 && uploadsDesign[0].base64
  );

  useEffect(() => {
    switch (activeTab) {
      case "clipart":
        setSelectedItem("");
        setActiveItem(allClipArts.length > 0 && allClipArts[0].name);
        setDefoultItem(allClipArts.length > 0 && allClipArts[0].url);
        break;
      case "design":
        setSelectedItem("");
        setActiveItem(uploadsDesign.length > 0 && uploadsDesign[0].name);
        setDefoultItem(uploadsDesign.length > 0 && uploadsDesign[0].base64);
        break;
      case "logo":
        setSelectedItem("");
        setActiveItem(uploadsLogo.length > 0 && uploadsLogo[0].name);
        setDefoultItem(uploadsLogo.length > 0 && uploadsLogo[0].base64);
      default:
        break;
    }
  }, [activeTab]);
  return (
    <Row className="hearderBar">
      <Col className="col-md-12">
        <div className="input-group">
          <div className="input-group-prepend">
            <span className="input-group-text">
              <SVGSearch />
            </span>
          </div>
          <input
            type="text"
            className="form-control"
            aria-label="Search"
            placeholder="Search "
            aria-describedby="inputGroup-sizing-lg"
          />
        </div>
      </Col>
      <Col className="md-12">
        <HeaderBarCustomButtons
          handaleTab={handaleTab}
          activeClass={activeClass}
        />
      </Col>
      <Col className="col-md-12">
        <div className="grid-item">
          <img
            className="img-fluid"
            src={
              selectedItem
                ? selectedItem.url
                  ? selectedItem.url
                  : selectedItem.base64
                : defoultItem
            }
          />
        </div>
      </Col>
    </Row>
  );
};
