import React from "react";
import { SVGNoData } from "../SVGs/SVGs";
export const NoData = ({ noDataMessage }) => {
  return (
    <div className="noData">
      <div className="noDataBlock">
        <div>
          <SVGNoData />
        </div>
        <span>{noDataMessage}</span>
      </div>
    </div>
  );
};
