import React from 'react'
import LogoWhite from "../assets/images/logoWhite.png";
import { SVGLogo } from "./SVGs/SVGs";
import { globalConstants } from "../services/globalVariables";
function LeftSide() {
    const url = `${globalConstants.STORE_BASE_URL}`;
    const urlPrevious = `${globalConstants.STORE_BASE_URL}/soreinformation/Productselect/markup/`;
    return (
        <div className="sideBar" >
            <div className="sideBarInner" >
                <div className="sideBarTop" >
                    <a href={url}>
                        {/* <img src={LogoWhite} alt="logoWhite" /> */}
                        <SVGLogo />
                    </a>
                </div >
                <div className="sideBarMiddle flex1" >
                    <h4>Risk-free. Stress-free.</h4>
                    <h2>
                        Get started with your own custom printed apparel and accessories store
                    </h2>
                    <ul className="listCheck d-none d-lg-block">
                        <li>
                            Set up your store in minutes. choose your products, your markup and start selling.
                        </li>
                        <li>
                            No hassle of managing production or shipment. We take care of all of that.
                        </li>
                        <li>
                            Get payouts at regular intervals based on your markup percentage.
                        </li>
                    </ul >
                </div >
                <div className="d-none d-lg-block">
                    <a href={urlPrevious} className="btn btn-outline-light">
                        <svg width="24" height="12" viewBox="0 0 24 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path className="fillHover" d="M0.274963 5.33638C0.275244 5.3361 0.275479 5.33577 0.275807 5.33549L5.17443 0.460497C5.54142 0.0952939 6.135 0.096653 6.50029 0.463684C6.86554 0.830668 6.86414 1.42425 6.49715 1.78949L3.20821 5.06249L23.0625 5.06249C23.5803 5.06249 24 5.48221 24 5.99999C24 6.51777 23.5803 6.93749 23.0625 6.93749L3.20826 6.93749L6.49711 10.2105C6.86409 10.5757 6.8655 11.1693 6.50025 11.5363C6.13495 11.9034 5.54132 11.9046 5.17439 11.5395L0.27576 6.66449C0.275479 6.66421 0.275244 6.66388 0.274916 6.6636C-0.0922562 6.29713 -0.091084 5.70163 0.274963 5.33638Z" fill="white"></path>
                        </svg>
                        <b>Previous</b>
                    </a>
                </div >
            </div >
        </div >
    )
}

export default LeftSide
