import React, { useState, useEffect } from "react";
import { SVGGrayTick } from "../../../../../components/SVGs/SVGs";
import Slider from "react-slick";
// import { SVGSliderNext } from "../../../../../components/SVGs/SVGs";
export const MainSectionProductCard = ({
  clipartData,
  handleClick,
  Product,
  handleSelectedProduct,
  key,
  data,
}) => {
  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    // prevArrow: <SVGSliderNext />,
    // nextArrow: "right",
  };
  // console.log(Product,"Productkkl");
  return (
    <div data-toggle="buttons" className="btn-group">
      <label className={`btn`}>
        <div className="items d-flex align-items-center justify-content-center">
          <Slider {...settings}>
            <img src={Product} className="img-fluid" />
            <img src={Product} className="img-fluid" />
          </Slider>
          <div
            className="isSelected "
            // onClick={() => handleSelectedProduct(data, key)}
          >
            <label className="checkInput sm">
              <input
                type="checkbox"
                name={data.productId}
                value={data.productId}
                onChange={(e) => handleSelectedProduct(data, key)}
                defaultChecked={data.isSelected}
                checked={data.isSelected}
              />
              <span className="checkInputCheck"></span>
            </label>
            {/* <SVGGrayTick /> */}
          </div>
        </div>
      </label>
    </div>
  );
};
