import React, { useEffect, useState } from "react";
import axios from "axios";
import { globalConstants } from "../../../../services/globalVariables";
export const ProductThumb = ({ product, handleSelectedProduct, index, selectedPreview }) => {
  const [currentProduct, setCurrentProduct] = useState({});
  const [loader, setLoader] = useState(true);

  const getSelectedProduct = async (requiredProducts) => {
    if (requiredProducts.length > 0) {
      const queryString = {
        productdata: requiredProducts,
      };
      try {
        const response = await axios.post(
          `${globalConstants.BASE_URL}customizerdata/`,
          queryString
        );
        const result = response.data;
        setCurrentProduct(result[0]);
        setLoader(false);
      } catch (err) {
        console.log(err);
        setLoader(false);
      }
    }
  };

  useEffect(() => {
    let firstProduct = {
      name: "sproid",
      value: product.product_id,
    };
    getSelectedProduct([firstProduct]);
  }, [product]);

  if (loader) return "Loading...";
  let slideImages = [];
  if (currentProduct?.side_images_info) {
    Object.keys(currentProduct.side_images_info).map(function (key, index) {
      slideImages.push(currentProduct.side_images_info[key]);
    });
  }

  return (
    <div className="col-6 ">
      <div className="imageViewer noBg">
        <div
          className={`imageBG ${selectedPreview == index ? "border" : ""}`}
          onClick={() => handleSelectedProduct({ ...product, ...currentProduct }, index)}
        >
          <img className="img-fluid" src={slideImages[0].image} />
        </div>
      </div>
    </div>
  );
};
