import React, { useState } from "react";
import { CollapsMultiSelect } from "./CollapsMultiSelect";
import { DropDownCatTitle } from "./DropDownCatTitle";
import { DropDownSubCatTitle } from "./DropDownSubCatTitle";

export const DropDownGroup = ({
  allProducts = [],
  item,
  handleClick,
  handleProductSelection,
  level
}) => {
  const [showDropDown, handleDropDown] = useState(false);

  const onChangeDropDown = (status) => {
    handleDropDown(!status);
  };

  return (
    <> 
    {level === "CATEGORY" ? <DropDownCatTitle
        allProducts={allProducts}
        item={item}
        showDropDown={showDropDown}
        onChangeDropDown={onChangeDropDown}
        handleProductSelection={handleProductSelection}
        level={level}
      /> : <DropDownSubCatTitle
      allProducts={allProducts}
      item={item}
      showDropDown={showDropDown}
      onChangeDropDown={onChangeDropDown}
      handleProductSelection={handleProductSelection}
      level={level}
    /> }
      
      <CollapsMultiSelect
        listArray={item}
        allProducts={allProducts}
        id={item.category_id}
        handleProductSelection={handleProductSelection}
        handleClick={handleClick}
        showDropDown={showDropDown}
      />
    </>
  );
};
