import React from "react";
import { SVGMinus, SVGPlus } from "../SVGs/SVGs";

export const DropDownSubCatTitle = ({
  item,
  handleProductSelection,
  allProducts = [],
  onChangeDropDown,
  showDropDown
}) => {
  let categoryProducts = allProducts.filter(
    (prod) => prod.subCategoryId === item.category_id
  );
  let categoryProductStatus =
    categoryProducts.filter((prod) => prod.isActive).length ===
    categoryProducts.length;

  return (
    <li>
      <label
        className="checkInput"
        onClick={() => handleProductSelection("SUBCATEGORY", item.category_id)}
      >
        <span className={categoryProductStatus ? "checkInputCheck active " : "checkInputCheck"}></span>
        <span className="checkInputText">{item.category_name}</span>
      </label>
      <span
        style={{ cursor: "pointer" }}
        onClick={() => onChangeDropDown(showDropDown)}
      >
        {showDropDown ? <SVGMinus /> : <SVGPlus />}
      </span>
    </li>
  );
};
