import React, { useContext, useEffect,useState } from "react";
import { Collapse } from "react-bootstrap";
import ProjectContext from "../../context/ProjectContext";
import { SVGMinus, SVGPlus } from "../SVGs/SVGs";
import { Collaps } from "./Collaps";
import { DropDownCatTitle } from "./DropDownCatTitle";
import { DropDownGroup } from "./DropDownGroup";
import { DropDownItem } from "./DropDownItem";

export const CollapsMultiSelect = ({ 
  listArray,
  selectSubCatProduct,
  allProducts,
  handleProductSelection,
  handleClick,
  showDropDown
}) => { 
  
  return (
    <Collapse in={showDropDown}>
      <ul style={{ listStyle: "none" }}>
        {listArray.product.map((item, index) => {
          return (
            <DropDownItem item={item} allProducts={allProducts} handleProductSelection={handleProductSelection} />
          );
        })}
        {listArray.subcategories &&
          listArray.subcategories.map((elm, index) => {
            return (
               <DropDownGroup 
                 item={elm} 
                key={index}
                allProducts={allProducts}
                handleClick={handleClick}  
                handleProductSelection={handleProductSelection} 
                level="SUBCATEGORY" />
               
            );
          })}
      </ul>
    </Collapse>
  );
};
