import React, { useContext, useEffect, useState } from "react";
import { Modal } from "react-bootstrap";
import { ModalHeader } from "../ModalChild/ModalHeader";
import { ProductPage } from "./ProductPage/ProductPage";
import { ArtworkPage } from "./ArtworkPage";
import { Name } from "./Name";
import ProjectContext from "../../../context/ProjectContext";
import { toPng, toSvg } from "html-to-image";
import ConfirmModal from "../../../components/confirmModal/ConfirmModal";
import axios from "axios";
import { globalConstants } from "../../../services/globalVariables";
import LoaderPage from "./../../../components/LoaderPage";
export const CustomizerProductModal = ({ setCustomizeProductModal }) => {
  const {
    products,
    handleProductVarient,
    allClipArts,
    multiSelectedProduct,
    allCategory,
    productVarient,
    selectedProduct,
    editMode,
    handleEditMode,
    uploadsDesign,
    uploadsLogo,
    checkedProductArt,
    handleCheckedProductArt,
    isTabChange,
    handleIsTabChange,
    modalDescription,
    handleModalDescription,
    customizedProduct,
    selectedProductSideSku,
    selectedProductCategories,
    nameSelectCategory,
    handleNameSelectCategory,
  } = useContext(ProjectContext);

  // console.log(selectedProduct, "selectProduct");

  const [activeTab, setActiveTab] = useState("products");
  const [activeClass, setActiveClass] = useState("products");
  const [varientProducts, setVarientProducts] = useState([]);
  const [modalConfirm, setModalConfirm] = useState(false);
  const [selectproductPreviwModal, setSelectproductPreviwModal] =
    useState(false);
  const [selectedProductForPreview, setselectedProductForPreview] =
    useState(selectedProduct);
  const [selectedPreview, setSelectedPreview] = useState(0);
  const [isClipArtsSelected, setIsClipArtsSelected] = useState(allCategory[0]);
  const [selectedClipart, setSelectedClipart] = useState({});
  const [takeScreenshot, setTakeScreenshot] = useState(false);
  // const [finalSelectedArt, setFinalSelectedArt] = useState();
  const [isConfrimModalDisabled, setIsConfirmModalDisabled] = useState(false);
  const [loader, setLoader] = useState(false);
  // const [isTabChange, setIsTabChange] = useState(false);
  const [dataArray, setDataArray] = useState([...productVarient]);

  const [sliderButton, setSliderButton] = useState("next");
  const [productSlide, setProductSlide] = useState(0);
  const urlSearchParams = new URLSearchParams(window.location.search);
  const params = Object.fromEntries(urlSearchParams.entries());

  const handleTab = (tabName) => {
    setActiveTab(tabName);
    setActiveClass(tabName);
  };

  // handle for select product in product tab
  const handleSelectedProduct = (product, index) => {
    setProductSlide(0);
    setSliderButton("next");
    if (!editMode) {
      setselectedProductForPreview(product);

      setSelectedPreview(index);
      const { image, product_id, product_name, side_images_info } = product;
      let tempData = {
        productImage: image,
        product_id,
        product_name,
        side_images_info,
      };
      setIsClipArtsSelected(tempData);
      if (checkedProductArt.length > 0) {
        handleEditMode(true);
      } else {
        handleEditMode(false);
      }
    } else {
      handleModalDescription(
        "Product will be saved with selected artwork. Do you wish to confirm?"
      );
      setModalConfirm(true);
    }
  };

  // handle for select art in product tab
  const handleSelectedArts = (status, item) => {
    // console.log(status, item, "status, item--------------");
    if (status) {
      if (!editMode) {
        handleCheckedProductArt([...checkedProductArt, item]);
        setSelectedClipart(item);
        handleEditMode(true);
      } else {
        handleModalDescription(
          "Product will be saved with selected artwork. Do you wish to confirm?"
        );
        setModalConfirm(true);
      }
    } else {
      let selectedProductArt = checkedProductArt.filter(function (i) {
        if (i.id !== item.id) {
          return i;
        } else {
          return false;
        }
      });
      handleEditMode(false);
      handleCheckedProductArt([...selectedProductArt]);
      setSelectedClipart({});
    }
  };

  // handle for art checked in product tab
  const checkArtItem = (id) => {
    let checkedProduct = checkedProductArt.filter(function (i) {
      if (i.id === id) {
        return i;
      } else {
        return false;
      }
    });
    if (checkedProduct.length === 1) {
      return true;
    } else {
      return false;
    }
  };

  useEffect(() => {
    let myObject2 = {
      categoryId: "",
      categoryName: "",
      productId: "",
      productName: "",
    };
    selectedProductCategories.forEach((i) => {
      i.product.forEach((j) => {
        if (j.categoryId == selectedProductForPreview.categoryId) {
          myObject2.categoryId = i.category_id;
          myObject2.categoryName = i.category_name;
          myObject2.productId = j.product_id;
          myObject2.productName = j.product_name;
        }
      });
    });
    handleNameSelectCategory(myObject2);
  }, [selectedProductForPreview]);

  // handle for create final product in product tab
  const handleSaveProduct = () => {
    setIsConfirmModalDisabled(true);
    let node = document.getElementById("productImageContainer");
    setTakeScreenshot(true);
    toPng(node, {
      allowTaint: false,
      useCORS: false,
      cacheBust: true,
    })
      .then(function (dataUrl) {
        var img = new Image();
        img.src = dataUrl;
        let timeStamp = new Date().getTime();
        let finalData = [
          {
            productVarientId: timeStamp,
            // ...tempData,
            // ...finalSelectedArt,
            artId: checkedProductArt[0].id,
            artName: checkedProductArt[0].name,
            productName: selectedProductForPreview.name,
            productId: selectedProductForPreview.id,
            name:
              selectedProductForPreview.name +
              "_" +
              checkedProductArt[0].name +
              "_" +
              timeStamp,
            isFinalProductSelected: true,
            finalProductImageUrl: img.src,
            categoryName: nameSelectCategory.categoryName,
            categoryId: nameSelectCategory.categoryId,
            isFinalCategorySelected: true,
            selectedProductSideSku: selectedProductSideSku,
          },
        ];

        console.log(finalData, "finalData");
        handleCheckedProductArt([]);
        handleEditMode(false);
        setTakeScreenshot(false); // after taking screenshot show canvas border
        handleProductVarient(finalData);
        setModalConfirm(false);
        setIsConfirmModalDisabled(false);
        if (isTabChange) {
          setActiveTab("name");
          setActiveClass("name");
          handleIsTabChange(false);
        }
        // setIsTabChange(false);
      })
      .catch(function (error) {
        console.error("Something went wrong!", error);
      });
    // }
  };

  // confirm modal close handle
  const handleClose = () => {
    setModalConfirm(false);
    handleEditMode(true);
    // handleCheckedProductArt([]);
    // setSelectedClipart({});
  };

  // handle modal next
  const handleNext = () => {
    if (editMode) {
      handleIsTabChange(true);
      handleModalDescription(
        "Product will be saved with selected artwork. Do you wish to confirm?"
      );
      setModalConfirm(true);
    } else {
      setActiveTab("name");
      setActiveClass("name");
    }
    // if (editMode) {
    //   setModalConfirm(true);
    // setVarientProducts();
    //   setActiveTab("name");
    //   setActiveClass("name");
    //   handleEditMode(false);
    // }
  };

  const handleDone = async () => {
    let finalResult = [];
    customizedProduct.forEach((element) => {
      let finalProduct = {
        productName: element.name,
        productImage: element.finalProductImageUrl,
        parentProductId: element.productId,
        selectedProductSideSku: element.selectedProductSideSku,
      };
      finalResult.push(finalProduct);
    });

    const data = {
      productdata: {
        ...params,
        products: finalResult,
      },
    };

    try {
      setLoader(true);
      const res = await axios.post(
        `${globalConstants.BASE_URL}saveproduct/`,
        data
      );
      if (res.data === "saved") {
        window.location.href = `${globalConstants.STORE_BASE_URL}/soreinformation/themeselect`;
        setLoader(false);
      } else {
        setLoader(false);
        // alert("Please try again later!");
      }
    } catch (err) {
      setLoader(false);
      if (
        err.response.data.message ===
        "URL key for specified store already exists."
      ) {
        alert(
          "Products exist with same name, Please change the name of the products."
        );
      }
    }
  };
  if (loader) return <LoaderPage />;
  return (
    <>
      <Modal.Header closeButton>
        <ModalHeader handleTab={handleTab} activeClass={activeClass} />
      </Modal.Header>
      <Modal.Body>
        <div className="tab-content d-flex flex-column h-100">
          {(() => {
            switch (activeTab) {
              case "products":
                return (
                  <ProductPage
                    setVarientProducts={setVarientProducts}
                    setModalConfirm={setModalConfirm}
                    modalConfirm={modalConfirm}
                    setSelectedClipart={setSelectedClipart}
                    selectedClipart={selectedClipart}
                    selectedProductForPreview={selectedProductForPreview}
                    selectedPreview={selectedPreview}
                    // sendFinalProductImageUrl={sendFinalProductImageUrl}
                    setTakeScreenshot={setTakeScreenshot}
                    handleSelectedArts={handleSelectedArts}
                    checkArtItem={checkArtItem}
                    handleSelectedProduct={handleSelectedProduct}
                    takeScreenshot={takeScreenshot}
                    handleSaveProduct={handleSaveProduct}
                    setSliderButton={setSliderButton}
                    sliderButton={sliderButton}
                    productSlide={productSlide}
                    setProductSlide={setProductSlide}
                  />
                );
              case "artwork":
                return <ArtworkPage />;
              case "name":
                return (
                  <Name dataArray={dataArray} setDataArray={setDataArray} />
                );
              default: {
                return "";
              }
            }
          })()}
        </div>
      </Modal.Body>
      <Modal.Footer className="align-items-center justify-content-between">
        <div>
          {/* <p className="d-none d-lg-block">
            * This primary design selection will appear as default on every
            product
          </p> */}
        </div>
        <button
          type="button"
          className="btn btn-secondary"
          id="doneCustomize"
          onClick={() => (activeClass == "name" ? handleDone() : handleNext())}
        >
          {activeClass == "name" ? <b>Done</b> : <b>Next</b>}
        </button>
      </Modal.Footer>

      <div style={{ zIndex: "9999" }}>
        <Modal
          show={modalConfirm}
          onHide={handleClose}
          className="confirmModal"
          centered
        >
          <ConfirmModal
            title="Confirm Product"
            description={modalDescription}
            handleConfirm={() => handleSaveProduct()}
            handleClose={() => {
              handleClose();
            }}
            takeScreenshot={takeScreenshot}
            disabled={isConfrimModalDisabled}
          />
        </Modal>
      </div>
    </>
  );
};
