import React, { useState, useContext } from "react";
import { Modal } from "react-bootstrap";
import { SVGMinus, SVGPlus } from "../../../components/SVGs/SVGs";
// import productPreview from "../../../JsonData/productPreview.json";
import ProjectContext from "../../../context/ProjectContext";

export const MultiSelectProductPreviewModal = () => {
  const { allCategory, handleMultiProduct, multiSelectedProduct } = useContext(ProjectContext);
  console.log("multiSelectedProduct: ", multiSelectedProduct);
  const [show, setShow] = useState(null);
  // const [accordian, setAccordian] = useState(false);
  const [checkedProduct, setCheckedProduct] = useState([]);

  const handleCollapse = (val) => {
    if (val) {
      setShow(val);
      // setAccordian(!accordian);
    }
  };
  const handleSelectedProduct = (status, product) => {
    console.log("status:- ", status);
    console.log("product:- ", product);
    console.log("checkedProduct:- ", checkedProduct);
    if (status) {
      setCheckedProduct([...checkedProduct, product]);
    }
    else {
      // const removeProduct = checkedProduct.filter(i => i.productId !== product.productId && i.categoryId !== product.categoryId);
      // setCheckedProduct(removeProduct);
    }
  };



  const handleSingleProductSelection = (checkedProduct) => {
    handleMultiProduct(checkedProduct);
    // handleHide();
  }
  return (
    <>
      <Modal.Header closeButton>
        {/* <h3 className="modal-title">Select a product to view</h3> */}
        {/* <span>Select a product to view</span> */}
      </Modal.Header>
      <Modal.Body>
        <div className="markupModalInner d-flex flex-column p-0">
          <div className="divTable d-flex flex-column flex1 overflowY">
            <div className="px24">
              <div className="divTableRow borderBottom py24">
                <span>Select a product to view</span>
              </div>
              <form action="POST" id="markupform">
                <div className="accordionCustom">
                  {allCategory.map((item) => (
                    <div className="accordionCustomItem borderBottom active" key={item.id}>
                      <div className="divTableRow py24">
                        <div className="divTableCol">
                          <div className="d-flex align-items-center">
                            <div
                              className="btnAccordion1"
                              onClick={() => handleCollapse(item.id)}
                              style={{ cursor: "pointer" }}
                            >
                              {show === item.id ? (
                                <SVGMinus />
                              ) : (
                                <SVGPlus />
                              )}
                            </div>
                            <p style={{ marginLeft: "12px" }}>
                              {/* Promotional Products */}
                              {item.name}
                            </p>
                          </div>
                        </div>
                        {/* <div className="divTableCol">
                          <input type="checkbox" name="cat" value="39" />
                        </div> */}
                      </div>
                      {show === item.id ? (
                        <div className="accordionCustomItemData">
                          <div className="accordionCustomItemDataContent">
                            <div className="accordionCustomSub">
                              {item &&
                                item.products.map((val) => (
                                  <div className="divTableRow" key={item.id + "-" + val.productId}>
                                    <div className="divTableCol">
                                      <div className="d-flex align-items-center">
                                        <p>{val.productName}</p>
                                      </div>
                                    </div>
                                    <div className="divTableCol">
                                      <input
                                        type="checkbox"
                                        name="multiSelectProduct"
                                        onChange={(e) => handleSelectedProduct(e.target.checked, val)}
                                      />
                                    </div>
                                  </div>
                                ))}
                            </div>
                          </div>
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  ))}
                </div>
              </form>
            </div>
          </div>
        </div>
      </Modal.Body>
      <Modal.Footer className="align-items-center justify-content-end">
        <button type="button" className="btn btn-secondary" onClick={() => handleSingleProductSelection(checkedProduct)} disabled={checkedProduct ? false : true}>Done</button>
      </Modal.Footer>
    </>
  );
};
