import React from "react";

export const ChooseClipartModalFooter = ({
  checkedClipArt,
  countSelectd,
  handleDone,
}) => {
  return (
    <>
      <p className="secondaryFont">
        <strong>{countSelectd}</strong> Cliparts selected
      </p>
      <button type="button" className="btn btn-secondary" onClick={handleDone}>
        <b>Done</b>
      </button>
    </>
  );
};
