import React, { useContext, useState } from "react";
import { useToasts } from "react-toast-notifications";

import ProjectContext from "../../context/ProjectContext";
import downArrorw from "../../assets/images/downarrow.svg";
import closebtn from "../../assets/images/closebtn.png";
import { SVGDownArrow, SVGUpArrow } from "../../components/SVGs/SVGs";

export const UploadLogo = ({ handleShuffle }) => {
  const { addToast } = useToasts();
  const { handleUploadLogo, uploadsLogo } = useContext(ProjectContext);
  const [showUploadLogo, setShowUploadLogo] = useState(false);

  const onUploadLogo = async (e) => {
    let filesToUpload = e.target.files.length;
    let fileData = [];
    let files = { id: "", name: "", type: "", base64: "" };
    let uploadedAt = new Date().getTime();
    for (let i = 0; i < filesToUpload; i++) {
      if (e.target.files[i].size <= 5242880) {
        const base64 = await convertBase64(e.target.files[i]);
        const id = uploadedAt + i;
        const name = e.target.files[i].name;
        const type = e.target.files[i].type;
        files = {
          id: id,
          name: name,
          type: type,
          base64: base64,
          isSelectedArts: false,
        };
        fileData.push(files);
      } else {
        addToast("File size more than 5MB", {
          appearance: "error",
        });
      }
    }
    const allFiles = uploadsLogo.concat(fileData);
    handleUploadLogo(allFiles);
  };
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };

  // handleRemoveLogo
  const handleRemoveLogo = (id) => {
    const removedUploadsLogo = uploadsLogo.filter(function (item) {
      if (item.id !== id) {
        return item;
      }
    });
    handleUploadLogo(removedUploadsLogo);
  };

  return (
    <div className="buttonSection">
      <div className="buttonBar d-flex align-items-start justify-content-between">
        <div>
          <p
            className="buttonTitle"
            onClick={() => setShowUploadLogo(!showUploadLogo)}
          >
            Logos
          </p>
          <p className="buttonSubTitle">
            Upload the logos you want to be available on your store
          </p>
        </div>
        <span
          className="buttonArrow"
          onClick={() => setShowUploadLogo(!showUploadLogo)}
        >
          {showUploadLogo ? <SVGUpArrow /> : <SVGDownArrow />}
        </span>
      </div>
      <div
        className={showUploadLogo ? "collapse show" : "collapse "}
        id="uploadTemplate"
      >
        <div className="collapseContent">
          <div className="d-flex align-items-center uploadBtn">
            <label
              className="btn btn-outline-dark mb-0"
              data-toggle="modal"
              data-target="#templatePreview"
            >
              <input
                type="file"
                name="Upload Logo"
                className="d-none"
                data-max-size=""
                accept=".jpeg, .png"
                multiple="multiple"
                onChange={(e) => onUploadLogo(e)}
                onClick={(event) => {
                  event.target.value = null;
                }}
              />
              <b>Upload Logo</b>
            </label>
          </div>
          {uploadsLogo.length > 0 && (
            <div className="card card-body  bg-light">
              <span className="uploadStatus darkStatus">
                <strong>{uploadsLogo.length}</strong> Logos uploaded
              </span>
            </div>
          )}

          {uploadsLogo && uploadsLogo.length > 0 && (
            <div className="fileViewer align-items-start">
              <div className="fileColumnViewer d-flex">
                {uploadsLogo &&
                  uploadsLogo.map((item) => (
                    <div className="fileView  mr-3" key={item.id}>
                      <span
                        className="closeButton"
                        onClick={() => handleRemoveLogo(item.id)}
                      >
                        <img src={closebtn} />
                      </span>
                      <img src={item.base64} alt={item.name} onClick={() => handleShuffle(item)} />
                    </div>
                  ))}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
