import React, { useContext, useState, useEffect } from "react";
import sampleImg from "../../../../assets/images/sampleImg.png";
import ImageCustomize from "../../ImageCustomizer/ImageCustomize";
import ProjectContext from "../../../../context/ProjectContext";
import Slider from "react-slick";
import ConfirmModal from "../../../../components/confirmModal/ConfirmModal";
import { Modal } from "react-bootstrap";
import { toPng } from "html-to-image";
import { SlideItem } from "./SlideItem";

export const MainProductPreview = ({
  selectedClipart,
  setSelectedClipart,
  selectedPreview,
  selectedProductForPreview,
  sendFinalProductImageUrl,
  setTakeScreenshot,
  takeScreenshot,
  modalConfirm,
  setModalConfirm,
  setCheckedProductArt,
  handleSaveProduct,
  sliderButton,
  setSliderButton,
  productSlide,
  setProductSlide,
}) => {
  const {
    selectedProduct,
    products,
    multiSelectedProduct,
    handleEditMode,
    editMode,
    checkedProductArt,
    handleModalDescription,
    selectedProductCategories,
    catData,
    handleCatData,
  } = useContext(ProjectContext);

  const [shuffleUrl, setShuffleUrl] = useState("");
  const [show, setShow] = useState(false);
  const [refreshCanvas, setRefreshCanvas] = useState(false);
  const [timer, setTimer] = useState(null);
  const [sliderImage, setSliderImage] = useState([]);
  const slickRef = React.useRef(Slider);
  const [imageObject, setImageObject] = useState({});
  // prevent borders of canvas to be included in screenshot

  function PrevButton({ onClick }) {
    const movePrev = () => {
      if (editMode) {
        handleModalDescription(
          "Artwork can be applied on one side of product only. On changing the side, artwork will get removed from previous side. Do you wish to proceed?"
        );
        setModalConfirm(true);
      } else {
        setSliderButton("next");
        slickRef.current.slickPrev();
      }
    };
    return (
      <button
        onClick={movePrev}
        className={`slick-prev slick-arrow ${
          sliderButton === "next" ? "slick-disabled" : ""
        }`}
      >
        Prev
      </button>
    );
  }

  function NextButton({ onClick }) {
    const moveNext = () => {
      if (editMode) {
        handleModalDescription(
          "Artwork can be applied on one side of product only. On changing the side, artwork will get removed from previous side. Do you wish to proceed?"
        );
        setModalConfirm(true);
      } else {
        setSliderButton("prev");
        slickRef.current.slickNext();
        setProductSlide(1);
      }
    };
    return (
      <button
        onClick={moveNext}
        className="slick-arrow slick-next"
        className={`slick-arrow slick-next ${
          sliderButton === "prev" ? "slick-disabled" : ""
        }`}
      >
        Next
      </button>
    );
  }

  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,

    initialSlide: productSlide,
    prevArrow: <PrevButton />,
    nextArrow: <NextButton />,
    afterChange: (current) => {
      setProductSlide(current);
    },
  };

  useEffect(() => {
    if (checkedProductArt.length > 0) {
      // consider it true in this case
      let imgUrl = checkedProductArt.filter((i) => i.base64);
      setShuffleUrl(
        selectedClipart.base64 ? selectedClipart.base64 : selectedClipart.url
      );
    } else {
      setShuffleUrl("");
    }
  }, [selectedClipart, shuffleUrl, checkedProductArt]);

  React.useEffect(() => {
    setRefreshCanvas(false);
    setTimeout(() => {
      setTimer(null);
      setRefreshCanvas(true);
    }, 100);
  }, [selectedProductForPreview, checkedProductArt]);

  useEffect(() => {
    let tempData = {
      category_name: "",
      category_id: "",
    };
    // console.log("selectedProductForPreview", selectedProductForPreview);
    // console.log("selectedProductCategories", selectedProductCategories);
    selectedProductCategories.filter((i) => {
      i.product.filter((j) => {
        if (j.product_id == selectedProductForPreview.id) {
          tempData.category_id = i.category_id;
          tempData.category_name = i.category_name;
          // console.log("i.category_name------", i.category_name);
          // tempData{ category_id = i.category_id }
          // handleCatData({
          //   categoryName: i.category_name,
          //   categoryId: i.category_id,
          // });
        }
      });
    });
    selectedProductForPreview.categoryName = tempData.category_name;
    selectedProductForPreview.categoryId = tempData.category_id;
    // console.log(tempData, "1234567890");
  }, [catData]);

  useEffect(() => {
    // console.log("selectedProductForPreview", selectedProductForPreview);
    let slideImg = [];
    if (selectedProductForPreview?.side_images_info) {
      Object.keys(selectedProductForPreview.side_images_info).map(function (
        key,
        index
      ) {
        slideImg.push(selectedProductForPreview.side_images_info[key]);
      });
    }
    setSliderImage(slideImg);
  }, [selectedProductForPreview, productSlide, selectedProductCategories]);

  useEffect(() => {
    slickRef.current.slickGoTo(productSlide);
  }, [productSlide]);

  return (
    <>
      <div className="imageBG flex-wrap">
        <div
          className={`imageContainer w-100 ${
            takeScreenshot ? "sliderArrowNone" : ""
          }`}
          id="productImageContainer"
          style={{ position: "relative" }}
        >
          {selectedProductForPreview && (
            <React.Fragment>
              <Slider ref={slickRef} {...settings} className="slickArrowY-M">
                {sliderImage.map((item, index) => (
                  <SlideItem
                    key={index}
                    item={item}
                    index={index}
                    sliderImage={sliderImage}
                    refreshCanvas={refreshCanvas}
                    shuffleUrl={shuffleUrl}
                    takeScreenshot={takeScreenshot}
                    imageObject={imageObject}
                    setImageObject={setImageObject}
                    productSlide={productSlide}
                    side_sku={item.side_sku}
                    id={index && `${index}`}
                  />
                ))}
              </Slider>
            </React.Fragment>
          )}
        </div>
      </div>
    </>
  );
};
