import React from 'react'

export const ModalHeader = ({ handleTab, activeClass }) => {
  return (
    <div className="row hearderBar no-gutters d-flex row-flex align-items-end">
      <div className="left-col">
        {/* <h3 className="modal-title">Customize product</h3> */}
        <h3>Customize product</h3>
      </div>
      <div className="right-col noBg">
        <ul className="nav nav-tabs">
          <li className="nav-item " onClick={() => handleTab('products')}>
            <a className={`nav-link ${activeClass === 'products' ? 'active' : null}`}>Products</a>
          </li>
          {/* <li className="nav-item" onClick={() => handleTab('artwork')}>
            <a className={`nav-link ${activeClass === 'artwork' ? 'active' : null}`}>Artwork</a>
          </li> */}
          <li className="nav-item" onClick={() => handleTab('name')}>
            <a className={`nav-link ${activeClass === 'name' ? 'active' : null}`}>Name</a>
          </li>
        </ul>
      </div>
    </div>
  )
}
