import React, { useState, useContext, useEffect } from "react";
import { Collapse } from "react-bootstrap";
import { unstable_renderSubtreeIntoContainer } from "react-dom";
import ProjectContext from "../../context/ProjectContext";
import { SVGMinus, SVGPlus } from "../SVGs/SVGs";
import { CollapsMultiSelect } from "./CollapsMultiSelect";
import { DropDownCatTitle } from "./DropDownCatTitle";
import { DropDownGroup } from "./DropDownGroup";

export const MultiSelectionModal = ({ openModal, setOpenModal }) => {
  const {
    allCategory,
    handleMultiProduct,
    multiSelectedProduct,
    selectedProductCategories,
  } = useContext(ProjectContext);
  const [listItemsArray, setListItemsArray] = useState(
    selectedProductCategories
  );
  const [catAllPro, setCatAllPro] = useState([]);

  const handleClick = (id) => {
    let tempsArray = [...listItemsArray];
    tempsArray.forEach((element) => {
      if (element.category_id === id) {
        element.showItem = !element.showItem;
      }
    });

    setListItemsArray(tempsArray);
  };
  useEffect(() => {
    let dataArray = [...selectedProductCategories];
    dataArray.forEach((element) => {
      let obj = element;
      obj.showItem = false;
    });
    setListItemsArray(dataArray);
  }, []);

  useEffect(() => {
    let selectAllPro = [];
    selectedProductCategories.forEach((i) => {
      i.product.map((item) => (item.categoryId = i.category_id));
      i.product.map((item) => (item.subCategoryId = null));
      selectAllPro = [...selectAllPro, ...i.product];
      if (i?.subcategories) {
        i.subcategories.forEach((j) => {
          j.product.map((item) => (item.categoryId = i.category_id));
          j.product.map((item) => (item.subCategoryId = j.category_id));
          selectAllPro = [...selectAllPro, ...j.product];
        });
      }
    });

    selectAllPro.map((item) => (item.isActive = false));
    setCatAllPro(selectAllPro);
  }, []);

  const handleProductSelection = (elementType, productId) => {
    let tempData = [...catAllPro];
    if (elementType === "PRODUCT") {
      tempData.forEach((item) => {
        if (item.product_id === productId) {
          item.isActive = !item.isActive;
        }
      });
    } else if (elementType === "CATEGORY") {
      tempData.forEach((item) => {
        if (item.categoryId === productId) {
          item.isActive = !item.isActive;
        }
      });
    } else if (elementType === "SUBCATEGORY") {
      tempData.forEach((item) => {
        if (item.categoryId === productId) {
          item.isActive = !item.isActive;
        }
      });
    }
    handleContextState(tempData)
    setCatAllPro(tempData);
  };

  const handleDropDownStatus = (status) => {
    setOpenModal(status);
  };

  const checkerAllSelect = catAllPro.filter((item) => item.isActive).length === catAllPro.length;
  const handleAllSelect = (status) => {
    let tempData = [...catAllPro];
    tempData.map((item) => (item.isActive = !status));
    
    handleContextState(tempData)
    setCatAllPro(tempData);
  };

  const handleContextState = (data) => {
    let selectedItems = data.filter(item=> item.isActive);
    let uniqueObjArray = [...new Map(selectedItems.map((item) => [item["product_id"], item])).values()];
    handleMultiProduct(uniqueObjArray)
  }

  return (
    <Collapse in={openModal}>
      <div className="productSelectionDropdown">
        <button
          className="productSelectionDropdownClose"
          onClick={() => handleDropDownStatus(false)}
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.150689 9.84906C0.250772 9.9499 0.3825 10 0.514228 10C0.64583 10 0.777557 9.94927 0.878394 9.84906L5.0002 5.72736L9.12236 9.84906C9.22257 9.9499 9.35417 10 9.4859 10C9.61687 10 9.74847 9.94927 9.84931 9.84906C10.0502 9.64815 10.0502 9.32242 9.84931 9.1215L5.72767 4.99991L9.84931 0.878372C10.0502 0.677332 10.0502 0.3516 9.84931 0.150686C9.64839 -0.0502285 9.32265 -0.0502285 9.12173 0.150686L5.00003 4.27228L0.878394 0.150686C0.677349 -0.0502285 0.351609 -0.0502285 0.150689 0.150686C-0.0502298 0.3516 -0.0502298 0.677332 0.150689 0.878372L4.27253 4.99976L0.150689 9.1215C-0.0502298 9.32242 -0.0502298 9.64815 0.150689 9.84906Z"
              fill="black"
            />
          </svg>
        </button>
        <p className="titleSelector f15">Select a product to view</p>
        <ul>
          <li>
            <label className="checkInput">
              <span className={checkerAllSelect ? "checkInputCheck active " : "checkInputCheck"}></span>
              <span
                className="checkInputText"
                onClick={(e) => handleAllSelect(checkerAllSelect)}
              >
                {/* {checkerAllSelect ? "Unselect All" : "Select All"} */}
                Select All
              </span>
            </label>
          </li>
          {listItemsArray &&
            listItemsArray.map((item, index) => {
              return (
                <DropDownGroup
                  item={item}
                  key={index}
                  allProducts={catAllPro}
                  handleClick={handleClick}
                  handleProductSelection={handleProductSelection}
                  level="CATEGORY"
                />
              );
            })}
        </ul>
      </div>
    </Collapse>
  );
};
