export const ChooseClipartModal = ({
  hide,
  checkedClipArt,
  setCheckedClipArt,
  setCountSelectd,
  countSelectd,
  setIsAllSelected,
  // isAllSelected,
  // searchInput,
  // allClipArts,
  // handleSelectAll,
  // handleSelected,
  // setSelectedTag,
  // selectedTag,
  // onSearchChange,
  // setSearchInput,
  // onSelectCategoryClick,
  clipartCat,
}) => {
  const { allClipArts, handleUploadClipArts, allClipArtsCategory,clipArts } =
    useContext(ProjectContext);
  const [selectedTag, setSelectedTag] = useState(null);
  const [searchInput, setSearchInput] = useState("");
  const [categories, setCategories] = useState([]);
  const [clipArts1, setClipArts1] = useState([]);

  const resetCategory = () => {
    let temp = [...clipArts];
    temp.forEach((c) => {
      c.isSelected = false;
    });
    setClipArts1(temp);
  };



  ///////////Firoz khan////////////////////

  let dummyArray = [];
  
  categories.forEach((item) => {
    if (item.isActive) {
      dummyArray.push(item.cat_sku);
    }
  });

  const result =
    dummyArray.length > 0
      ? allClipArts.filter((item) => dummyArray.includes(item.cat_sku))
      : allClipArts;

      const searchResult = searchInput !=="" ? result.filter(item =>  item.name.toLowerCase().includes(searchInput.toLowerCase())) : result

  ///////////Firoz khan////////////////////




  // select category handle
  const onSelectCategoryClick = (categoryID) => {
    const updatedItem = categories.filter(function (item) {
      if (item.cat_id === categoryID) {
        item.isActive = !item.isActive;
      }
      return item;
    });
    setCategories(updatedItem);
    // resetCategory();
    // if (category !== "All") {
    //   let matched = allClipArts.filter((clipArt) => {
    //     return category === clipArt.cat_sku;
    //   });
    //   setSelectedTag(matched[0].cat_sku);
    //   // matched.forEach((item) => (item.isActive = true));
    //   setClipArts1(matched);
    //   console.log(matched)
    // } else {
    //   setClipArts1(category);
    // }
  };

  const handleSelected = (isCheck, id) => {
    let totalSelected = [];
    for (let i = 0; i < allClipArts.length; i++) {
      if (allClipArts[i].image_id == id) {
        allClipArts[i] = { ...allClipArts[i], isChecked: isCheck };
      }
    }
    totalSelected = allClipArts.filter((check) => {
      if (check.isChecked) {
        return true;
      }
    });
    console.log(totalSelected);
    setCountSelectd(totalSelected.length);
    handleUploadClipArts(totalSelected);
  };
const [categoryArray, setcategoryArray] = useState([]);
  const handleSelectAll = (isCheck) => {
    let totalSelected = [];
    for (let i = 0; i < searchResult.length; i++) {
        searchResult[i] = { ...searchResult[i], isChecked: isCheck };
    }
    totalSelected = searchResult.filter((check) => {
      if (check.isChecked) {
        return true;
      }
    });
    setcategoryArray(searchResult)
    console.log(searchResult,"searchResult6789iudfghj");
    console.log(totalSelected,"totalSelected");
    setCountSelectd(totalSelected.length);
    handleUploadClipArts(totalSelected);
  };

  const setIsSelected = (temp, boolean) => {
    temp.forEach((c) => {
      c.isSelected = boolean;
    });
  };

  // input search handle
  const onSearchChange = (e) => {
    setSearchInput(e.target.value);
  };

  useEffect(() => {
    let items = [...allClipArtsCategory];
    items.forEach((item) => {
      item.isActive = false;
    });
    setCategories(items);
  }, [allClipArtsCategory]);

const isAllSelected=()=>{
  if(clipArts.length===searchResult.length){
    return true
  }else{
    return false
  }
}

  return (
    <>
      <Modal.Header>
        <ChooseClipartModalHeader
          onSelectCategoryClick={onSelectCategoryClick}
          clipartCat={categories}
          hide={hide}
          searchInput={searchInput}
          setSearchInput={setSearchInput}
          onSearchChange={onSearchChange}
          selectedTag={selectedTag}
          setSelectedTag={setSelectedTag}
        />
      </Modal.Header>
      <Modal.Body>
        <ChooseClipartModalBody
          handleSelected={handleSelected}
          handleSelectAll={handleSelectAll}
          allClipArts={clipartCat}
          searchInput={searchInput}
          categories={categories}
          searchResult={searchResult}
          categoryArray={categoryArray}
          isAllSelected={isAllSelected}
        />
      </Modal.Body>
      <Modal.Footer className=" align-items-center justify-content-between">
        <ChooseClipartModalFooter
          checkedClipArt={checkedClipArt}
          countSelectd={countSelectd}
          handleDone={hide}
        />
      </Modal.Footer>
    </>
  );
};
