import React, { useContext, useEffect, useState } from "react";
import ProjectContext from "../../../../context/ProjectContext";
import { ClipsPreview } from "../../ModalChild/ClipsPreview";
import { PreviewDesignArt } from "./PreviewDesignArt";
import { SearchBox } from "../../ModalChild/SearchBox";
import { UploadLogoClip } from "../../ModalChild/UploadLogoClip";

export const ArtWorksApplied = ({
  handleSelectedArts,
  handleArtworkSelected,
  checkArtItem,
}) => {
  const { productVarient, allClipArts,clipArts } = useContext(ProjectContext);
  const [activeTab, setActiveTab] = useState("design");
  const [activeClass, setActiveClass] = useState("design");
  const [selectedItem, setSelectedItem] = useState();
  const [activeItem, setActiveItem] = useState("");
  const [searchInput, setSearchInput] = useState("");
  const [clipArtsData, setClipArtsData] = useState([]);


  const handaleTab = (tabName) => {
    setActiveTab(tabName);
    setActiveClass(tabName);
  };

  const handleSelectItem = (item) => {
    setActiveItem(item.name);
    handleArtworkSelected(item);
  };

  // input search handle
  const onSearchChange = (e) => {
    setSearchInput(e.target.value);
  };
  // to filter clipart for customize product
  useEffect(() => {
    let tempData = allClipArts.filter((item) => item.isChecked);
    // console.log(tempData);
    setClipArtsData(tempData);
  }, []);

  return (
    <div className="right-col">
      <div className="row hearderBar">
        <div className="col-xl-4 appheader">
          <p className="apptitle">Art applied to this product</p>
          <p className="appsubtitle">
            {productVarient.length} Artworks Applied
          </p>
        </div>
        <div className="col-xl-4 mt-3 mt-xl-0 pr-xl-4 pl-xl-0">
          <SearchBox
            placeholder="Search"
            value={searchInput}
            onChange={(e) => onSearchChange(e)}
          />
        </div>
        <div className="col-xl-4 mt-4 mt-xl-0 px-xl-0">
          <UploadLogoClip handaleTab={handaleTab} activeClass={activeClass} />
        </div>
      </div>
      <div className="row chooseProductPreview flex-grow-1">
        <div className="col-md-12 flex-grow-1 d-flex flex-column">
          <PreviewDesignArt
            activeTab={activeTab}
            handleSelectItem={handleSelectItem}
            activeItem={activeItem}
            handleSelectedArts={handleSelectedArts}
            clipArtsData={clipArtsData}
            searchInput={searchInput}
            setSearchInput={setSearchInput}
            checkArtItem={checkArtItem}
          />
        </div>
      </div>
    </div>
  );
};
