import React, { useContext } from "react";
import { Row, Col } from "react-bootstrap";
import { NoData } from "../../../../components/NoData";
import { SVGIsSelected } from "../../../../components/SVGs/SVGs";

export const ProductItem = ({
  handleSelectItem,
  activeItem,
  itemsArray,
  activeTab,
  handleSelectedArts,
  noDataMessage,
  checkArtItem,
  setSearchInput,
  searchInput,
}) => {
  return (
    <>
      <div className="info-block block-info clearfix filterItemsGrid flex-grow-1">
        {itemsArray.length > 0 ? (
          itemsArray
            .filter((item) => {
              if (setSearchInput == "") {
                return item;
              } else if (
                item.name.toLowerCase().includes(searchInput.toLowerCase())
              ) {
                return item;
              }
            })
            .map((item, index) => (
              <div
                key={item.id}
                data-toggle="buttons"
                className="btn-group"
              // onClick={() => handleSelectItem(item)}
              >
                {/* {console.log(item,"ite3m")} */}
                <label
                  className={`btn ${activeItem === item.name ? "active" : ""}`}
                >
                  <div className="items d-flex align-items-center justify-content-center">
                    <div className="isSelected">
                      {/* <SVGIsSelected /> */}
                      <label class="checkInput sm">
                        <input
                          type="checkbox"
                          name={item.id}
                          // defaultChecked={item.isSelectedArts}
                          checked={checkArtItem(item.id)}
                          onChange={(e) =>
                            handleSelectedArts(
                              e.target.checked,
                              item
                              // itemsArray
                            )
                          }
                        />
                        <span class="checkInputCheck"></span>
                      </label>


                    </div>
                    <img
                      src={activeTab === "clipart" ? item.url : item.base64}
                    />
                  </div>
                </label>
              </div>
            ))
        ) : (
          <NoData noDataMessage={noDataMessage} />
        )}
      </div>

      {/* {itemsArray&&itemsArray.length>0?itemsArray.map((item, index) => {
          return (
            <Col xs={6}>
              <div className={`grid-item ${activeItem ===item.name?"activeCheck":null}`} onClick={()=>handleSelectItem(item)}>
                <img className="img-fluid" src={activeTab==="clipart" ?item.url:item.base64} key={index}/>
              </div>
            </Col>
          );
        }):null} */}
    </>
  );
};
