import React, { useState } from "react";
import {
  SVGPreview,
  SVGClose,
  SVGGreenTick,
  SVGGrayTick,
  SVGExpand,
  SVGCollapse,
} from "../../../../../components/SVGs/SVGs";
import { SelectProductPreviewModal } from "../../SelectProductPreviewModal";
import { Modal } from "react-bootstrap";
import { MultiSelectionModal } from "../../../../../components/DropDownModal/MultiSelectionModal";

export const HeaderBar = () => {
  const [selectproductPreviwModal, setSelectproductPreviwModal] =
    useState(false);
    const [openModal, setOpenModal] = useState(false)
  return (
    <div>
      <div className="row hearderBar" >
        <div className="col-md-10 appheader">
          <p className="apptitle">Preview products</p>
        </div>
        <div className="col-md-2">
              
          <div className="imageViewer noBg" >
            <button
              className="btn btn-clear selectorBtn"
              type="button"
              onClick={() => setOpenModal(!openModal)}
            >
              <SVGPreview />
            </button>
            <MultiSelectionModal openModal={openModal} setOpenModal={setOpenModal}/>
          </div>
        </div>
      </div>
      <Modal
        show={selectproductPreviwModal}
        onHide={() => setSelectproductPreviwModal(false)}
        size="xl"
        className="customizeProduct selectProductPreviwModal"
      >
        <SelectProductPreviewModal />
      </Modal>
    </div>
  );
};
