import React, { useContext, useEffect, useState } from "react";
import { Collapse } from "react-bootstrap";
import ProjectContext from "../../context/ProjectContext";
import { SVGMinus, SVGPlus } from "../SVGs/SVGs";
import { Collaps } from "./Collaps";

export const CollapsSingleSelect = ({
  listArray,
  showItems,
  setOpen,
  onProductSelection,
}) => {
  const { handleCurrentProduct, handleSelectedProducts } =
    useContext(ProjectContext);
  const [subCat, setSubCat] = useState([]);

  const handleChange = (item) => {
    setOpen(false);
    // handleCurrentProduct(item);
    let product = {
      name: "sproid",
      value: item.product_id,
    };
    onProductSelection([product]);
    handleSelectedProducts(item);
  };

  const selectProduct = (product) => {
    setOpen(false);
    handleCurrentProduct(product); 
  };
  useEffect(() => {
    if (listArray.subcategories) {
      let subArray = listArray.subcategories;
      subArray.forEach((element) => {
        let obj = element;
        obj.showSubProduct = false;
      });
      setSubCat(subArray);
    }
  }, []);

  const handleSubCat = (id) => {
    let handleArray = [...subCat];
    handleArray.forEach((element) => {
      if (element.category_id === id) {
        element.showSubProduct = !element.showSubProduct;
      }
    });
    setSubCat(handleArray);
  };

  return (
    <Collapse in={showItems}>
      <ul style={{ listStyle: "none" }}>
        {listArray.product.map((item, index) => {
          return (
            <React.Fragment key={index}>
              <li key={index}>
                <label className="checkInput">
                  <input
                    type="radio"
                    name="name"
                    value={item}
                    onChange={() => handleChange(item)}
                  />
                  <span className="checkInputCheck"></span>
                  <span className="checkInputText">{item.product_name}</span>
                </label>
              </li>
            </React.Fragment>
          );
        })}
        {subCat &&
          subCat.map((elm, key) => {
            return (
              <>
                <li onClick={() => handleSubCat(elm.category_id)} key={key}>
                  {elm.category_name}
                  <span>{elm.showSubProduct ? <SVGMinus /> : <SVGPlus />}</span>
                </li>
                <Collaps elm={elm} selectProduct={selectProduct} type={"radio"}/>
              </>
            );
          })}
      </ul>
    </Collapse>
  );
};
