import React from "react";
import { Collapse } from "react-bootstrap";
import { DropDownItem } from "./DropDownItem";


export const Collaps = ({elm,showItems,selectSubCatProduct,type,allProducts, handleProductSelection}) => {
  return (
    <>
      <Collapse in={elm.showItem}>
        <ul style={{ listStyle: "none" }}>
          {elm.product.map((item, index) => <DropDownItem  handleProductSelection={handleProductSelection} allProducts={allProducts} item={item} /> )}
        </ul>
      </Collapse>
    </>
  );
};
