import React from "react";

export const HeaderBarCustomButtons = ({ handaleTab,activeClass }) => {
  
  return (
    <div className="custome-btn">
      <div className="">
        <div
          data-toggle="buttons"
          className="btn-group"
          onClick={()=>handaleTab('design')}
        >
          <a className={`btn btn-outline-primary ${activeClass === 'design' ? 'active' : null}`}>Upload</a>
        </div>
        <div data-toggle="buttons" className="btn-group" onClick={()=>handaleTab('logo')}>
          <a className={`btn btn-outline-primary ${activeClass === 'logo' ? 'active' : null}`}>Logos </a>
        </div>
        <div
          data-toggle="buttons"
          className="btn-group"
          onClick={()=>handaleTab('clipart')}
        >
          <a className={`btn btn-outline-primary ${activeClass === 'clipart' ? 'active' : null}`}>Cliparts </a>
        </div>
      </div>
    </div>
  );
};
