import React, { useState, useEffect, useContext } from "react";
// import Clipart from '../../../../../assets/images/clipart1.svg';
import Clipart from "../../../../../assets/images/product/productImg1.jpg";
import Clipart2 from "../../../../../assets/images/clipart2.svg";
import Clipart3 from "../../../../../assets/images/storeCreatedLogo.png";
import Clipart4 from "../../../../../assets/images/selectProductsTShirt.png";
import { MainSectionProductCard } from "./MainSectionProductCard";
import ProjectContext from "../../../../../context/ProjectContext";

export const MainSectionProductCards = ({
  allChecked,
  productArray,
  handleSelectedProduct,
}) => {
  const { multiSelectedProduct } = useContext(ProjectContext);
  // console.log(productArray, "productArray");
  const [clipartData, setClipartData] = useState([
    {
      clipart: Clipart,
      isChecked: false,
    },
    {
      clipart: Clipart2,
      isChecked: false,
    },
    {
      clipart: Clipart3,
      isChecked: false,
    },
    {
      clipart: Clipart4,
      isChecked: false,
    },
  ]);

  // const handleSelectedProduct = (data) => {
  //   const { categoryId, categoryName, image, productId, productName, sides } =
  //     data;
  //   // console.log(product);
  //   let tempData = {
  //     categoryId,
  //     categoryName,
  //     productImage: image,
  //     productId,
  //     productName,
  //     sides,
  //   };
  //   // setClipartData(tempData);
  //   // console.log(data);
  //   // let temp = [...clipartData];
  //   // temp.forEach((item) => {
  //   //   if (item.clipart === data.clipart) {
  //   //     data.isChecked = !data.isChecked;
  //   //   }
  //   // });
  //   // setClipartData(temp);
  // };

  useEffect(() => {
    let temp = [...clipartData];
    temp.forEach((item) => {
      item.isChecked = allChecked;
    });
    setClipartData(temp);
  }, [allChecked]);

  return (
    <div>
      {multiSelectedProduct.length > 0 &&
        multiSelectedProduct.map((data, index) => {
          // console.log(data, "data");
          return (
            <MainSectionProductCard
              key={index}
              Product={data.image}
              data={data}
              handleSelectedProduct={handleSelectedProduct}
            />
          );
        })}
    </div>
  );
};
