import React, { useContext } from "react";
import { ClipartCategories } from "./ChooseClipartComponents/ClipartCategories";
import { SVGClipartClose } from "../../../../components/SVGs/SVGs";
import { SearchBox } from "../SearchBox";
import ProjectContext from "../../../../context/ProjectContext";

export const ChooseClipartModalHeader = ({
  onSelectCategoryClick,
  clipartCat,
  hide,
  searchInput,
  onSearchChange,
  selectedTag,
  setSelectedTag,
}) => {
  const { allClipArts } = useContext(ProjectContext);
  return (
    <>
      <div className="row hearderBar no-gutters">
        <div className="col-lg-6 col-xl-8">
          <h4 className="modal-title">Choose clipart</h4>
          <div className="filterItems custome-btn">
            <div className="info-block block-info clearfix">
              
              {clipartCat.map((clipart, index) => (
                <div
                  key={clipart.cat_id}
                  data-toggle="buttons"
                  className="btn-group"
                  onClick={() => onSelectCategoryClick(clipart.cat_id)}
                >
                  <label
                    // className="btn btn-outline-primary "
                    className={`btn btn-outline-primary ${
                      clipart.isActive && "active"
                    }`}
                  >
                    {clipart.title}
                  </label>
                </div>
              ))}
            </div>
          </div>
        </div>
        <div className="col-lg-6 col-xl-4 mt-4 mt-lg-0">
          <SearchBox
            placeholder="Search"
            value={searchInput}
            onChange={(e) => onSearchChange(e)}
          />
        </div>
      </div>
      <span
        type="button"
        className="close outside"
        data-dismiss="modal"
        aria-label="Close"
        style={{ position: "absolute" }}
        onClick={hide}
      >
        <SVGClipartClose />
      </span>
    </>
  );
};
