import React, { useRef, useEffect, useState } from 'react'
import ImageCustomize from "../../ImageCustomizer/ImageCustomize";

export const SlideItem = ({ 
    item, 
    index, 
    sliderImage,
    refreshCanvas,
    shuffleUrl,
    takeScreenshot,
    imageObject,
    setImageObject,
    productSlide,
    side_sku
}) => {
    const [selectedProd,setSelectedProd] = useState({
        height: "",
        width: ""
    });

    const imageRef = useRef();
    useEffect(() => {
        setTimeout(() => {
            let obj = {
                width: Math.round((item.drawing_area_width * imageRef.current.width) / 100),
                height: Math.round((item.drawing_area_height * imageRef.current.height) / 100)
            };
            setSelectedProd(obj);
        }, 800);
    }, [productSlide]);

    return (
        <React.Fragment key={item.index}>
            <img className="img-fluid" src={item.image} alt="" ref={imageRef}  />
            {sliderImage.length > 0 && refreshCanvas && (
                <ImageCustomize
                    top={item.drawing_area_top}
                    left={item.drawing_area_left}
                    width={item.drawing_area_width}
                    height={item.drawing_area_height}
                    clipartUrl={shuffleUrl}
                    takeScreenshot={takeScreenshot}
                    id="productModalCanvas"
                    id={index && `${index}`}
                    imageObject={imageObject}
                    setImageObject={setImageObject}
                    productSlide={productSlide}
                    dimensionsCanvas={selectedProd}
                    side_sku={side_sku}
                />
            )}
        </React.Fragment>
    )
}