import React, { useContext } from "react";
import { Row, Col } from "react-bootstrap";

export const Items = ({
  handleSelectItem,
  activeItem,
  itemsArray,
  activeTab,
  handleSelectedArtWorks,
}) => {
  return (
    <>
      <div className="info-block block-info clearfix filterItemsGrid">
        {itemsArray &&
          itemsArray.map(
            (item, index) =>
              item.isChecked && (
                <div
                  key={index}
                  data-toggle="buttons"
                  className="btn-group"
                  onClick={() => handleSelectItem(item)}
                >
                  <label
                    className={`btn ${
                      activeItem === item.name ? "active" : ""
                    }`}
                  >
                    <div className="items d-flex align-items-center justify-content-center">
                      <div className="isSelected">
                        {/* <SVGIsSelected /> */}
                        <input
                          type="checkbox"
                          name={item.id}
                          value={item.id}
                          defaultChecked={item.isSelectedArts}
                          checked={item.isSelectedArts}
                          onChange={(e) =>
                            handleSelectedArtWorks(
                              e.target.checked,
                              item,
                              itemsArray
                            )
                          }
                        />
                      </div>
                      <img
                        src={activeTab === "clipart" ? item.url : item.base64}
                      />
                    </div>
                  </label>
                </div>
                // ) : (
                //   "No cliparts selected"
              )
          )}
      </div>
      {/* <Row>
        {itemsArray && itemsArray.length > 0
          ? itemsArray.map((item, index) => {
              return (
                <Col xs={6}>
                  <div
                    className={`grid-item ${
                      activeItem === item.name ? "activeCheck" : null
                    }`}
                    onClick={() => handleSelectItem(item)}
                  >
                    <img
                      className="img-fluid"
                      src={activeTab === "clipart" ? item.url : item.base64}
                      key={index}
                    />
                  </div>
                </Col>
              );
            })
          : null}
      </Row> */}
    </>
  );
};
