import React, { useContext, useState } from "react";
import { useToasts } from "react-toast-notifications";

import ProjectContext from "../../context/ProjectContext";
import { SVGDownArrow, SVGUpArrow } from "../../components/SVGs/SVGs";
import closebtn from "../../assets/images/closebtn.png";

export const UploadDesign = ({ handleShuffle }) => {
  const { addToast } = useToasts();
  const { handleUploadDesign, uploadsDesign } = useContext(ProjectContext);
  const [showUploadDesign, setShowUploadDesign] = useState(true);

  const onUploadDesign = async (e) => {
    let filesToUpload = e.target.files.length;
    let fileData = [];
    let files = { id: "", name: "", type: "", base64: "" };
    let uploadedAt = new Date().getTime();
    for (let i = 0; i < filesToUpload; i++) {
      if (e.target.files[i].size <= 5242880) {
        const base64 = await convertBase64(e.target.files[i]);
        const id = uploadedAt + i;
        const name = e.target.files[i].name;
        const type = e.target.files[i].type;
        files = {
          id: id,
          name: name,
          type: type,
          base64: base64,
          isSelectedArts: false,
        };
        fileData.push(files);
      } else {
        addToast("File size more than 5MB", {
          appearance: "error",
        });
      }
    }
    const allFiles = uploadsDesign.concat(fileData);
    handleUploadDesign(allFiles);
  };
  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };
      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  };

  const handleDeleteUploadDesign = (id) => {
    const removedUploadsDesign = uploadsDesign.filter(function (item) {
      if (item.id !== id) {
        return item;
      }
    });
    handleUploadDesign(removedUploadsDesign);
  };

  return (
    <div className="buttonSection">
      <div className="buttonBar d-flex align-items-start justify-content-between">
        <div>
          <p
            className="buttonTitle"
            onClick={() => setShowUploadDesign(!showUploadDesign)}
          >
            Upload your design
          </p>
          <p className="buttonSubTitle">Upload images for your store</p>
        </div>
        <span
          className="buttonArrow"
          onClick={() => setShowUploadDesign(!showUploadDesign)}
        >
          {showUploadDesign ? <SVGUpArrow /> : <SVGDownArrow />}
        </span>
      </div>
      <div
        className={showUploadDesign ? "collapse show" : "collapse "}
        id="uploadDesign"
      >
        <div className="collapseContent">
          <div className="d-flex align-items-center uploadBtn">
            <label className="btn btn-outline-dark mb-0">
              <input
                type="file"
                name="Upload Logo"
                className="d-none"
                data-max-size=""
                accept=".jpeg, .png"
                multiple="multiple"
                onChange={(e) => onUploadDesign(e)}
                onClick={(event) => {
                  event.target.value = null;
                }}
              />
              <b>Upload</b>
            </label>
            <span>Max file size 5MB</span>
          </div>
          {uploadsDesign && uploadsDesign.length > 0 && (
            <div className="card card-body  bg-light">
              <span class="uploadStatus darkStatus">
                <strong>{uploadsDesign && uploadsDesign.length}</strong> Designs
                uploaded
              </span>
            </div>
          )}

          {/* <span className="uploadStatus">
              <strong>{uploadsDesign && uploadsDesign.length}</strong> Files
              uploaded
            </span> */}
          {uploadsDesign && uploadsDesign.length > 0 && (
            <div className="fileViewer align-items-start">
              {/* <UploadedDesignPreview /> */}
              <div className="fileColumnViewer d-flex">
                {uploadsDesign.map((item) => (
                  <div className="fileView  mr-3" key={item.id}>
                    <span
                      className="closeButton"
                      onClick={() => handleDeleteUploadDesign(item.id)}
                    >
                      <img src={closebtn} alt="close" />
                    </span>
                    <img src={item.base64} alt={item.name} onClick={() => handleShuffle(item)}/>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
