import React from "react";
import { ArtworkLeftSection } from "./ArtworkLeftSection/ArtworkLeftSection";
import { ArtworkRightSection } from "./ArtworkRightSection/ArtworkRightSection";

export const ArtworkPage = () => {
  const handleSelectedArtWorks = (isSelected, item, allArtData) => {
    console.log("handleSelectedArtWorks", item);
    // let allArtData = uploadsDesign.concat(uploadsLogo, clipArts);
    // console.log(allArtData, "allArtData");
    for (let i = 0; i < allArtData.length; i++) {
      if (allArtData[i].id == item.id) {
        allArtData[i] = { ...allArtData[i], isSelectedArts: isSelected };
        // setIsSelectedArts(true);
      } else {
        allArtData[i] = { ...allArtData[i], isSelectedArts: false };
      }
    }

    const { id, isChecked, name, type, url } = item;
    let tempData = {
      artName: name,
      artType: type,
      artUrl: url,
      isChecked,
    };

    let varientId = new Date().getTime();
    let finalData = [
      {
        productVarientId: varientId,
        ...tempData,
        // ...isClipArtsSelected,
      },
    ];
  };
  const handleSelectedProduct = (product) => {
    console.log(product);

    const { categoryId, categoryName, image, productId, productName, sides } =
      product;
    let tempData = {
      categoryId,
      categoryName,
      productImage: image,
      productId,
      productName,
      sides,
    };
    // handleProductVarient(finalData);
  };
  return (
    <div className="tab-pane active" id="artwork-tab">
      <div className="row  no-gutters d-flex row-flex">
        <ArtworkLeftSection handleSelectedArtWorks={handleSelectedArtWorks} />
        <ArtworkRightSection handleSelectedProduct={handleSelectedProduct} />
      </div>
    </div>
  );
};
