import React, { useState, useContext, useEffect } from "react";
import { Collapse } from "react-bootstrap";
import ProjectContext from "../../context/ProjectContext";
import { SVGMinus, SVGPlus } from "../SVGs/SVGs";
import { CollapsSingleSelect } from "./CollapsSingleSelect";

export const SingleSelectionModal = ({
  openModal,
  setOpen,
  onProductSelection,
}) => {
  const { allCategory, selectedProductCategories } = useContext(ProjectContext);
  const [listItemsArray, setListItemsArray] = useState(
    selectedProductCategories
  );

  useEffect(() => {
    setListItemsArray(selectedProductCategories);
  });

  const handleClick = (item) => {
    let tempsArray = [...listItemsArray];

    tempsArray.forEach((element) => {
      if (element.category_id === item.category_id) {
        element.showItem = !element.showItem;
      }
    });

    setListItemsArray(tempsArray);
  };

  useEffect(() => {
    let dataArray = [...selectedProductCategories];
    dataArray.forEach((element) => {
      let obj = element;
      obj.showItem = false;
    });
    setListItemsArray([...dataArray]);
  }, []);
  return (
    <Collapse in={openModal}>
      <div className="productSelectionDropdown">
        <button
          className="productSelectionDropdownClose"
          onClick={() => setOpen(false)}
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.150689 9.84906C0.250772 9.9499 0.3825 10 0.514228 10C0.64583 10 0.777557 9.94927 0.878394 9.84906L5.0002 5.72736L9.12236 9.84906C9.22257 9.9499 9.35417 10 9.4859 10C9.61687 10 9.74847 9.94927 9.84931 9.84906C10.0502 9.64815 10.0502 9.32242 9.84931 9.1215L5.72767 4.99991L9.84931 0.878372C10.0502 0.677332 10.0502 0.3516 9.84931 0.150686C9.64839 -0.0502285 9.32265 -0.0502285 9.12173 0.150686L5.00003 4.27228L0.878394 0.150686C0.677349 -0.0502285 0.351609 -0.0502285 0.150689 0.150686C-0.0502298 0.3516 -0.0502298 0.677332 0.150689 0.878372L4.27253 4.99976L0.150689 9.1215C-0.0502298 9.32242 -0.0502298 9.64815 0.150689 9.84906Z"
              fill="black"
            />
          </svg>
        </button>
        <p className="titleSelector f15">Select a product to view</p>
        <ul>
          {listItemsArray &&
            listItemsArray.map((item, index) => {
              return (
                <React.Fragment key={index}>
                  <li
                    key={index}
                    onClick={() => handleClick(item)}
                    style={{ cursor: "pointer" }}
                  >
                    <span className="oneLineText">
                      {item.category_name}
                    </span>
                    <span>{item.showItem ? <SVGMinus /> : <SVGPlus />}</span>
                  </li>
                  <CollapsSingleSelect
                    listArray={item}
                    showItems={item.showItem}
                    id={item.id}
                    setOpen={setOpen}
                    onProductSelection={onProductSelection}
                  />
                </React.Fragment>
              );
            })}
        </ul>
      </div>
    </Collapse>
  );
};
