import React from "react";

export const DropDownItem = ({
  item,
  handleProductSelection,
  allProducts = [],
}) => {
  let productStatus = allProducts.filter(
    (prod) => prod.product_id === item.product_id && prod.isActive
  ).length;
  return (
    <li onClick={() => handleProductSelection("PRODUCT", item.product_id)}>
      <label className="checkInput">
        <span className={productStatus ? "checkInputCheck active " : "checkInputCheck"}></span>
        <span className="checkInputText">{item.product_name}</span>
      </label>
    </li>
  );
};
