import React, { useState, useContext } from "react";
import { SVGGrayTickLarge } from "../../../../../components/SVGs/SVGs";
import { MainSectionProductCards } from "./MainSectionProductCards";
import ProjectContext from "../../../../../context/ProjectContext";

export const MainSection = ({ handleSelectedProduct }) => {
  const { multiSelectedProduct } = useContext(ProjectContext);

  const [allChecked, setAllChecked] = useState(false);
  return (
    <div className="row chooseProductArtPreview">
      <div className="col-md-12 ">
        <div className="info-block block-info clearfix filterItemsGrid">
          <div className="row filterItemsGridHeader">
            <div className="col-md-12 ">
              <div data-toggle="buttons" className="btn-group">
                <label
                  className={`btn d-flex ${allChecked ? "active" : ""}`}
                  onClick={() => setAllChecked(!allChecked)}
                >
                  <div className="isSelectedGroup">
                    <SVGGrayTickLarge />
                  </div>
                  <span className="apptitle"> Select All</span>
                </label>
              </div>
            </div>
          </div>
          {multiSelectedProduct.length > 0 &&
            multiSelectedProduct.map((item, index) => {
              return (
                <>
                  {/* <label class="checkInput">
                  <input
                    type="checkbox"
                    name={item.categoryName}
                    value={item}
                    // onChange={() => handleChange(item)}
                  />
                  <span className="checkInputCheck "></span>
                  <span class="checkInputText">{item.categoryName}</span>
                </label> */}
                </>
              );
            })}
          <MainSectionProductCards
            allChecked={allChecked}
            handleSelectedProduct={handleSelectedProduct}
          />
        </div>
      </div>
    </div>
  );
};
