import React, { useContext, useState, useEffect } from "react";
import ProjectContext from "../../../../context/ProjectContext";
import { ProductItem, } from "./ProductItem";

export const PreviewDesignArt = ({
  activeTab,
  activeItem,
  handleSelectItem,
  handleSelectedArts,
  clipArtsData,
  searchInput,
  setSearchInput,
  checkArtItem,
}) => {
  const { uploadsDesign, allClipArts, uploadsLogo,clipArts } =
    useContext(ProjectContext);

  return (
    <>
      {(() => {
        switch (activeTab) {
          case "design":
            return (
              <ProductItem
                itemsArray={uploadsDesign}
                handleSelectItem={handleSelectItem}
                activeItem={activeItem}
                activeTab={activeTab}
                handleSelectedArts={handleSelectedArts}
                noDataMessage={"No designs available right now."}
                searchInput={searchInput}
                checkArtItem={checkArtItem}
              />
            );
          case "logo":
            return (
              <ProductItem
                itemsArray={uploadsLogo}
                handleSelectItem={handleSelectItem}
                activeItem={activeItem}
                activeTab={activeTab}
                handleSelectedArts={handleSelectedArts}
                noDataMessage={"No logo available right now."}
                searchInput={searchInput}
                checkArtItem={checkArtItem}
              />
            );
          case "clipart":
            return (
              <ProductItem
                itemsArray={clipArts}
                handleSelectItem={handleSelectItem}
                activeItem={activeItem}
                activeTab={activeTab}
                handleSelectedArts={handleSelectedArts}
                noDataMessage={"No cliparts available right now."}
                checkArtItem={checkArtItem}
                searchInput={searchInput}
              />
            );
          default: {
            return;
          }
        }
      })()}
    </>
  );
};
