import React, { useContext, useEffect, useState } from "react";

import { ArtWorksApplied } from "./ArtWorksApplied";
import { ProductList } from "../../ModalChild/ProductList";
import { MainProductPreview } from "./MainProductPreview";
import {
  SVGIsSelected,
  SVGPreview,
  SVGRightArrow,
} from "../../../../components/SVGs/SVGs";
import { Modal } from "react-bootstrap";
import { SelectProductPreviewModal } from "../SelectProductPreviewModal";
import { MultiSelectionModal } from "../../../../components/DropDownModal/MultiSelectionModal";
import { SingleSelectionModal } from "../../../../components/DropDownModal/SingleSelectionModal";
import ProjectContext from "../../../../context/ProjectContext";
import sampleImg from "../../../../assets/images/sampleImg.png";
import ConfirmModal from "../../../../components/confirmModal/ConfirmModal";
import { ProductThumb } from "./productThumb";

export const ProductPage = ({
  setVarientProducts,
  modalConfirm,
  setModalConfirm,
  setSelectedClipart,
  selectedClipart,
  selectedProductForPreview,
  selectedPreview,
  sendFinalProductImageUrl,
  setTakeScreenshot,
  handleSelectedArts,
  checkArtItem,
  handleSelectedProduct,
  takeScreenshot,
  handleSaveProduct,
  productImageContainer,
  sliderButton,
  setSliderButton,
  productSlide,
  setProductSlide,
}) => {
  const {
    handleProductVarient,
    multiSelectedProduct,
    allCategory,
    selectedProduct,
    editMode,
    handleEditMode,
  } = useContext(ProjectContext);

  const [selectproductPreviwModal, setSelectproductPreviwModal] =
    useState(false);
  const [openModal, setOpenModal] = useState(false);

  return (
    <>
      <div
        className="tab-pane d-flex flex-column flex-grow-1 active h-100"
        id="products-tab"
      >
        <div className="row  no-gutters d-flex row-flex flex-grow-1 h-100 justify-content-between">
          <div className="left-col overflowY-scroll">
            <div className="row modalCustomizeRow">
              <div className="col-md-12">
                <div className="imageViewer noBg">
                  <div className="d-flex align-items-center w-100 mb-2 justify-content-between">
                    <div className="f15 secondaryFont textBlack ml-lg-5 pl-lg-5">
                      Art can be applied only on
                      <br />
                      <b>one</b> side of the Product.
                    </div>
                    <button
                      className="btn btn-clear selectorBtn"
                      type="button"
                      onClick={() => setOpenModal(!openModal)}
                      aria-controls="example-collapse-text"
                      aria-expanded={openModal}
                    >
                      <SVGPreview />
                    </button>
                  </div>
                  <MultiSelectionModal
                    openModal={openModal}
                    setOpenModal={setOpenModal}
                  />
                  <MainProductPreview
                    selectedClipart={selectedClipart}
                    setSelectedClipart={setSelectedClipart}
                    selectedPreview={selectedPreview}
                    selectedProductForPreview={selectedProductForPreview}
                    sendFinalProductImageUrl={sendFinalProductImageUrl}
                    takeScreenshot={takeScreenshot}
                    setTakeScreenshot={setTakeScreenshot}
                    setModalConfirm={setModalConfirm}
                    modalConfirm={modalConfirm}
                    //   checkedProductArt={checkedProductArt}
                    //   setCheckedProductArt={handleCheckedProductArt}
                    handleSaveProduct={handleSaveProduct}
                    productImageContainer={productImageContainer}
                    setSliderButton={setSliderButton}
                    sliderButton={sliderButton}
                    productSlide={productSlide}
                    setProductSlide={setProductSlide}
                  />
                </div>
              </div>
            </div>
            <div className="row modalCustomizeRowItems">
              {multiSelectedProduct.map((product, index) => (
                <ProductThumb
                  product={product}
                  handleSelectedProduct={handleSelectedProduct}
                  index={index}
                  selectedPreview={selectedPreview}
                />
              ))}
            </div>
          </div>
          <ArtWorksApplied
            handleSelectedArts={handleSelectedArts}
            checkArtItem={checkArtItem}
          />
        </div>
      </div>

      <Modal
        show={selectproductPreviwModal}
        onHide={() => setSelectproductPreviwModal(false)}
        size="xlg"
        dialogClassName="fullModal"
        className="customizeProduct customize"
      >
        <div
          style={{ padding: "60px 48px", maxWidth: "1400px", height: "100%" }}
        >
          <SelectProductPreviewModal />
        </div>
      </Modal>
    </>
  );
};
