import React, { useState, useContext } from "react";
import { fabric } from "fabric";
import ProjectContext from "../../../context/ProjectContext";

let canvas;
export default function ImageCustomizer({
  top,
  left,
  width,
  height,
  clipartUrl,
  id,
  takeScreenshot,
  productSlide,
  side_sku,
  dimensionsCanvas,
  clickedOutside
}) {
  const { selectedProduct, handleSelectedProductSideSku } =
    useContext(ProjectContext);
  const [includeBorder, setIncludeBorder] = useState(false);
  const { useEffect } = React;
  const canvasWidth = dimensionsCanvas.width;
  const canvasHeight = dimensionsCanvas.height;
  const getBase64FromUrl = async (url) => {
    const data = await fetch(url);
    const blob = await data.blob();
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.readAsDataURL(blob);
      reader.onloadend = () => {
        const base64data = reader.result;
        resolve(base64data);
      };
    });
  };

  useEffect(() => {
    setIncludeBorder(takeScreenshot ? false : true);
  }, [takeScreenshot, productSlide]);

  useEffect(() => {
    if (id == productSlide) {
      canvas = new fabric.Canvas(`${productSlide}`, {});
    }
  }, [selectedProduct, productSlide]);

  useEffect(() => {
    
  }, [clickedOutside]);

  useEffect(() => {
    if (id == productSlide) {
      async function fetchData() {
        handleSelectedProductSideSku(side_sku);
        canvas.clear();
        let url = await getBase64FromUrl(clipartUrl);
        fabric.Image.fromURL(url, function (img) {
          let sizes = Resizing(img, canvas);
          let width = (canvas.width / sizes.width) * (canvas.width / (img.width + 150));
          let img1 = img.set({
            scaleX: width,
            scaleY: width,
            crossOrigin: "*",
            opacity: 1,
            backgroundColor: "transparent",
            renderOnAddRemove: true,
          });
          canvas.on("mouse:out", function (e) {
            if (!e.target) {
              canvas.clear();
              canvas.add(img1);
            }
          });
          canvas.on("mouse:move", function (e) {
            if (
              e.pointer.x > canvas.width - 20 ||
              e.pointer.y > canvas.height - 20
            ) {
              canvas.clear();
            }
            canvas.add(img1);
          });
          canvas.centerObject(img);
          canvas.add(img1);
          canvas.renderAll();
        });

        return () => {
          canvas.dispose();
        };
      }
      if (clipartUrl && clipartUrl.length > 0) fetchData();
      else canvas.clear();
    }
  }, [clipartUrl, selectedProduct, productSlide]);

  const Resizing = (img, canvas) => {
    let maxWidth = canvas.width;
    let maxHeight = canvas.height;
    let ratio = 0;
    let width = img.width;
    let height = img.height;
    let h = 0;
    let w = 0;
    if (width > maxWidth && width > height) {
        ratio = width / height;
        h = maxWidth/ratio;
        w = maxWidth;
    } else  if (height > maxHeight && height > width){
        ratio = height / width;
        h = maxHeight;
        w = maxHeight/ratio;
    } else {
        w = maxWidth;
        h = maxHeight;
    }
    return {height: h, width: w};
  }

  if(!canvasWidth || !canvasHeight ) return "Loading...";
  return (
    <div
      className="canvasArea"
      style={{
        position: "absolute",
        top: parseInt(top) + "%",
        left: parseInt(left) + "%",
        width: {canvasWidth},
        height: {canvasHeight},
      }}
    >
      <canvas
        id={id}
        width={canvasWidth}
        height={canvasHeight}
        style={{
          border: `${
            includeBorder ? "1px solid black" : "1px solid transparent"
          }`,
        }}
      />
    </div>
  );
}
